.tv-med{
  background-color: $blockBg;
  &__inner{
    padding: 1em 0 6.25em;
    max-width: 48.5em;
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__list{
    margin-top: 2.5em;
    gap: .5em;
    li{
      &:before{
        width: 1.11em;
        height: 1.11em;
      }
    }
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
  &__btn{
    width: 16.64em;
    height: 3.41em;
    img{
      display: block;
      width: .9em;
      flex-shrink: 0;
      margin-right: .65em;
    }
  }
}

.tv-med2{
  padding: 6.25em 0 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: 56.72% auto;
    gap: 3em;
  }
  .content-text{
    blockquote{
      padding: .75em;
      font-size: 1em;
      margin-top: 7.5em;
      &:before,
      &:after{
        width: .625em;
        height: .625em;
      }
    }
  }
  &__steps{
    padding: 2.5em;
    background: radial-gradient(62.23% 670.3% at 91.8% 13.33%, #E8E2DA 0%, #E7E1D9 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    box-shadow: 0 .25em 1.25em rgba(119, 110, 112, 0.1);
    border-radius: .75em;
  }
  &__step{
    margin-top: 2.5em;
    display: flex;
    &-num{
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      margin-right: 1em;
      font-size: 1em;
      width: 3em;
      height: 3em;
      background-color: $primary;
      color: #fff;
    }
    &-top{
      display: flex;
      align-items: center;
      min-height: 3em;
    }
    &-bottom{
      display: flex;
      margin-top: 1.375em;
    }
    &-btn{
      display: flex;
      align-items: center;
      img{
        width: 1.125em;
        flex-shrink: 0;
        margin-right: .5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .tv-med{
    &__inner{
      padding: 2.1875em 0 3em;
    }
    &__info{
      gap: .625em;
    }
    &__list{
      gap: 1.5em;
      margin-top: 3em;
    }
    &__nav{
      margin-top: 3em;
    }
    &__btn{
      width: 100%;
    }
  }

  .tv-med2{
    padding: 3em 0 1.875em;
    &__grid{
      grid-template-columns: 100%;
      gap: 3em;
    }
    .content-text{
      blockquote{
        margin-top: 4em;
      }
    }
    &__steps{
      padding: 1.5em;
      background: $blockBg;
    }
    &__step{
      margin-top: 1.25em;
      &-num{
        width: 2.43em;
        height: 2.43em;
        font-size: .875em;
        background: #F2E8F9;
        color: $primary;
        margin-right: .71em;
      }
      &-top{
        min-height: 2.125em;
      }
      &-bottom{
        margin-top: .625em;
      }
    }
  }
}
