.qa{
  padding: 0 0 6.25em;
  .doctor-info__inner{
    margin-top: 2.5em;
  }
  &__filter{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .filter{
    padding: 0;
    width: unset;
    margin-top: -1.25em;
  }
  .consult-collection__grid{
    grid-template-columns: 100%;
    display: grid;
    gap: 2.5em;
  }
  .consult{
    padding: 0;
  }
  .sale-consult{
    padding: 0;
    background-color: transparent;
  }
  @media screen and (min-width: 577px){
     .sale-consult{
       .swiper{
         &-wrapper{
           grid-template-columns: 100%;
           display: grid;
           gap: 2.5em;
         }
       }
     }
  }
}

.qa2{
  padding: 6.25em 0 3.125em;
  .doctor-info__right{
    padding-top: 6em;
  }
  @media screen and (min-width: 577px){
    .sales-collection{
      padding: 0;
      .swiper{
        &-wrapper{
          grid-template-columns: 100%;
          display: grid;
          gap: 2.5em;
        }
      }
      .container{
        padding: 0;
      }
    }
    .sale-consult__slider{
      margin-top: 1.5em;
    }
  }
}

.actual-articles{
  padding: 6.25em 0;
  background-color: $blockBg;
  &__desc{
    margin-top: 1.25em;
  }
  &__slider{
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(3,calc((100% / 3) - (2em / 3)));
          gap: 1em;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.actual-card{
  &__groups{
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    flex-shrink: 0;
    width: 2.125em;
    height: 2.125em;
    margin-right: .625em;
    img{
      border-radius: 50%;
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: drop-shadow(0px .0625em .75em rgba(0, 0, 0, 0.19));
    }
  }
  &__text{
    margin-top: .625em;
  }
}

.article-top{
  padding-bottom: 1.5em;
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-bottom: 1.25em;
  }
  &__nav{
    display: none;
  }
}

.qa-article{
  padding: 3.125em 0;
  .doctor-info__right{
    padding-top: .875em;
  }
  .article{
    padding: 0;
  }
}

.article{
  .author,
  .content-text{
    margin-top: 3em;
  }
  &__stat{
    margin-top: 4.5em;
  }
  &__rating{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
    &-num{
      font-size: 1.25em;
      line-height: 120%;
      font-weight: 400;
      color: $primary;
      margin-left: .5em;
    }
  }
  &__vk{
    margin-top: 4.25em;
    img{
      width: 100%;
    }
    &-mob{
      display: none;
    }
  }
  &__nav{
    display: flex;
    margin-top: 4.25em;
  }
}

.other-qa{
  padding: 3.125em 0;
  &__groups{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: 2.125em;
    max-width: 48.5em;
  }
  &__group{
    &:not(:first-child){
      padding-top: 1.25em;
      border-top: 1px solid $lightGray;
    }
  }
  &__subtitle{
    line-height: 150%;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
  &__link{
    color: $black;
    img{
      width: 1.29em;
      margin-right: .575em;
    }
    &:hover{
      color: $gray;
    }
  }
  &__more{
    margin-top: 1.75em;
  }
}


@media screen and (max-width: 576px){
  .qa{
    padding: 1em 0 1.875em;
    .doctor-info__inner{
      margin: 0;
    }
    &__filter{
      flex-direction: column;
      margin-top: 2em;
    }
    .consult__btn{
      order: -1;
      width: 100%;
      margin: 0;
    }
    .filter{
      margin-top: 1.5em;
      width: calc(100% + .625em);
    }
    .sale-consult{
      width: calc(100% + 3em);
      margin-left: -1.5em;
      padding: 5em 1.5em 0 1.5em;
    }
  }

  .qa2{
    padding: 3.75em 0 1.875em;
    .sales-collection{
      padding: 0;
    }
    .sale-consult__slider{
      margin-top: 1.5em;
    }
    .consult{
      padding: 0;
    }
  }

  .actual-articles{
    padding: 3em 0;
    .swiper{
      &-slide{
        width: 16.125em;
        padding-right: .75em;
      }
    }
  }

  .actual-card{
    &__groups{
      gap: 1.25em;
    }
  }

  .qa-article{
    padding: 1.875em 0;
    .doctor-info__right{
      padding-top: 3.75em;
    }
  }

  .article{
    .author,
    .content-text{
      margin-top: 1.5em;
    }
    &__stat{
      margin-top: 3em;
    }
    &__rating{
      margin-top: 1.25em;
    }
    &__vk{
      margin: 3em 0 0 -1.5em;
      width: calc(100% + 3em);
      &-desktop{
        display: none;
      }
      &-mob{
        display: block;
      }
    }
    &__nav{
      margin-top: 3em;
    }
    &__btn{
      width: 100%;
    }
  }

  .article-top{
    padding: 0;
    &>.container{
      padding: 0;
    }
    &__info{
      padding: 1em 1.5em 1.5em;
      gap: .625em;
      background-color: $blockBg;
    }
    &__nav{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: .875em;
    }
    &__back{
      width: 1.75em;
      height: 1.75em;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__views{
      display: flex;
      align-items: center;
      img{
        width: 1em;
        flex-shrink: 0;
        margin-right: .375em;
      }
    }

    .tags{
      overflow-x: auto;
      padding: 0 1.5em 1.125em;
      flex-wrap: nowrap;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    .tag{
      flex-shrink: 0;
      margin-top: 0;
    }
  }

  .other-qa{
    padding: 1.875em 0;
    &__groups{
      margin-top: 1.5em;
    }
    &__group{
      &:not(:first-child){
        padding-top: .625em;
      }
    }
    &__more{
      margin-top: -.25em;
    }
  }
}
