.modal {
    background: rgba($black, .9);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    @media screen and (min-width: 577px) {
        text-align: center;
        &:before {
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
        &__dialog {
            display: inline-block;
            text-align: left;
            vertical-align: middle;
        }
    }

    &--active {
        opacity: 1;
        pointer-events: unset;
    }

    &__dialog {
        margin: auto;
        background-color: #fff;
        border-radius: 1.25em;
        width: 30em;
        max-width: calc(100% - 2em);
        padding: 4em 2.5em 2.5em;
        position: relative;
    }
    &__close{
        font-size: 1em;
        position: absolute;
        top: .875em;
        right: 1.5em;
        width: 2em;
        height: 2em;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            width: .875em;
        }
    }
    &__title{
        text-align: center;
    }
    &__form{
        margin: 2.5em auto 0;
        max-width: 25em;
        width: 100%;
        &-group{
            display: grid;
            grid-template-columns: 100%;
            gap: 1.25em;
            &:not(:last-child){
                margin-bottom: 1.875em;
            }
        }
    }
    .step__check{
        margin-top: .625em;
    }
    &__nav{
        margin-top: 1.25em;
        display: flex;
        justify-content: center;
    }
    &__btn{
        img{
            flex-shrink: 0;
            width: 1.2em;
            margin-right: .5em;
        }
    }
    &__clinics{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: .625em;
    }
    &__clinic{
        position: relative;
        &-inner{
            padding: 1.25em;
            border: 1px solid $lightGray;
            border-radius: .5em;
            height: 100%;
        }
        &-radio{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            cursor: pointer;
            z-index: 2;
            opacity: 0;
            &:checked{
                &~.modal__clinic-inner{
                    background: rgba(179, 111, 222, 0.1);
                    border-color: $primary;
                    color: $primary;
                    &:before{
                        opacity: 1;
                    }
                    .gray-text{
                        color: $primary;
                    }
                }
            }
        }
        &-info{
            display: grid;
            grid-template-columns: 100%;
            gap: .625em;
        }
    }
    &__checks{
        margin-top: -.5em;
        display: grid;
        grid-template-columns: 100%;
        gap: 1em;
    }
    &__team{
        display: flex;
    }
    .selection-card__images{
        margin: 0 .625em 0 0;
        align-items: unset;
    }
    .selection-card__img{
        font-size: 1em;
        width: 2.2em;
        height: 2.2em;
        border-width: 1px;
        background: $lightGray;
        display: flex;
        align-items: center;
        justify-content: center;
        &:not(:first-child){
            margin-left: -1.1875em;
        }
        svg{
            width: 1.125em;
            height: auto;
        }
    }
}

.thank-modal{
    &__icon{
        display: flex;
        justify-content: center;
        margin-bottom: 1.875em;
        img{
            width: 2.75em;
        }
    }
    &__info{
        display: grid;
        grid-template-columns: 100%;
        gap: 1.25em;
        text-align: center;
    }
    &__props{
        margin-top: 1.875em;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1.25em;
    }
    &__prop{
        &--full{
            grid-column: 1/-1;
        }
    }
    &__bottom{
        margin-top: 2.5em;
        padding: 1.5em;
        background-color: $blockBg;
        border-radius: .875em;
        text-align: center;
        display: grid;
        grid-template-columns: 100%;
        gap: .5em;
    }
    &__nav{
        display: flex;
        justify-content: center;
        margin-top: .75em;
    }
    &__btn{
        img{
            width: 1.125em;
            margin-left: .5em;
            display: block;
            flex-shrink: 0;
        }
    }
}

.example-modal{
    .modal__dialog{
        width: 56.25em;
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - (3.75em / 3)));
        gap: 1.25em;
        margin-top: 1.875em;
    }
    &__nav{
        margin-top: 1.875em;
    }
    &__btn{
        width: 100%;
        height: 3.5em;
    }
}

.example-card{
    padding: 1.25em;
    border: 1px solid $lightGray;
    border-radius: .75em;
    &__img{
        padding-bottom: 81%;
        position: relative;
        margin-bottom: .625em;
        img{
            width: 100%;
            height: 100%;
            position: absolute;
            object-fit: cover;
            top: 0;
            left: 0;
            border-radius: .5em;
        }
    }
}

.qa-modal{
    &__text{
        text-align: center;
        display: grid;
        grid-template-columns: 100%;
        gap: 1em;
    }
    &__buttons{
        display: grid;
        grid-template-columns: 100%;
        gap: 1.25em;
        margin-top: 1.25em;
    }
    &__btn{
        color: rgba(255, 255, 255, 0.8);
        &--vk{
            background: #668EDB;
        }
        &--tg{
            background: #3ABDE7;
        }
        svg{
            width: 1.33em;
            margin-right: .56em;
            height: auto;
        }
    }
}

.tv-modal{
    &__info{
        text-align: center;
        display: grid;
        grid-template-columns: 100%;
        gap: 1.25em;
    }
    &__nav{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 1.25em;
    }
    &__btn{
        &:not(:last-child){
            margin-right: 1.25em;
        }
    }
}

.cookie-modal{
    position: fixed;
    z-index: 10;
    right: 2em;
    bottom: 2em;
    width: 32.75em;
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 0 .25em 1.25em rgba(0, 0, 0, 0.15);
    backdrop-filter: blur(.5em);
    border-radius: .875em;
    padding: 1.5em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &._show{
        opacity: 1;
        pointer-events: unset;
    }
    &__content{
        display: grid;
        grid-template-columns: 100%;
        gap: 1.25em;
    }
    &__nav{
        display: flex;
        align-items: center;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            margin-top: 20px;
            padding: 4em 1.5em 1.5em;
        }
        &__close{
            right: 50%;
            transform: translateX(50%);
        }
        &__form{
            margin-top: 1.875em;
            &-group{
                &:not(:last-child){
                    margin-bottom: 2.5em;
                }
            }
            &-title{
                margin-bottom: -.45em;
            }
        }
        .step__check{
            margin: 0;
        }
        &__btn{
            width: 100%;
        }
        &__clinic{
            &-inner{
                padding: .675em;
            }
        }
        &__checks{
            margin: 0;
        }
    }

    .thank-modal{
        &__icon{
            margin-bottom: 1.5em;
        }
        &__info{
            gap: .625em;
        }
        &__bottom{
            margin-top: 3em;
        }
        &__btn{
            width: 100%;
        }
    }

    .example-modal{
        &__grid{
            grid-template-columns: 100%;
            gap: .625em;
        }
    }

    .example-card{
        display: flex;
        align-items: center;
        padding: .625em;
        &__img{
            flex-shrink: 0;
            width: 5.625em;
            height: 5.625em;
            padding: 0;
            margin: 0 1.25em 0 0;
        }
    }

    .tv-modal{
        &__btn{
            &:not(:last-child){
                margin-right: 1.43em;
            }
        }
    }

    .cookie-modal{
        width: 100%;
        max-width: calc(100% - 3em);
        right: 1.5em;
        bottom: 1.5em;
        &__content{
            gap: .5em;
        }
        &__nav{
            margin-top: .75em;
        }
    }
}
