.calc-result{
  padding-bottom: 6.25em;
  background-color: $blockBg;
  &__nav{
    display: flex;
    align-items: center;
  }
  &__back{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1.75em;
    height: 1.75em;
    margin-right: 2.5em;
    font-size: 1em;
    img{
      width: .75em;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
    margin-top: 5em;
  }
}

.result-card{
  padding: 2.5em;
  background: #FFFFFF;
  box-shadow: 0 .25em 1.25em rgba(119, 110, 112, 0.1);
  border-radius: .75em;
  position: relative;
  &__badge{
    display: flex;
    justify-content: flex-end;
    padding-top: 2.5em;
    margin: 0 -2.5em -2.5em 0;
    .m-badge{
      position: static;
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    &:not(:first-child){
      margin-top: 2.5em;
    }
  }
  &__img{
    margin-top: 1.25em;
    max-width: 13.0625em;
    img{
      width: 100%;
    }
  }
  &__text{
    margin-top: 2.5em;
    ul{
      li{
        &:before{
          background-color: $primary;
        }
      }
    }
    .text12{
      line-height: 133%;
    }
  }
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: .625em;
  }
  &__item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    &--bg{
      padding: 1.25em;
      background-color: $blockBg;
      border-radius: .625em;
    }
  }
  &__spoiler{
    margin-top: 1.25em;
    .spoiler-card{
      &__show{
        padding: .875em 0;
      }
    }
    .result-card__text{
      display: grid;
      grid-template-columns: 100%;
      gap: 1.125em;
      margin: 0;
      ul{
        gap: .5em;
        margin: 0 !important;
      }
    }
  }
  &__price{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
    margin-top: 2.5em;
  }
  &__price2{
    margin-top: 1.25em;
  }
}

.calc-price{
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__info2{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
    ul{
      gap: .875em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__nav2{
    margin-top: 2em;
    display: flex;
  }
  &__btn{
    img{
      width: 1.125em;
      margin-right: .5em;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
  &__item{
    background: #FFFFFF;
    box-shadow: 0 .25em 1.25em  rgba(119, 110, 112, 0.15);
    border-radius: .75em;
    padding: 1.5em;
    display: flex;
    flex-direction: column;
    &-title{
      max-width: 8.75em;
      margin-bottom: 1.25em;
    }
    &-nav{
      display: flex;
      margin-top: auto;
    }
    &-link{
      font-size: .75em;
    }
  }
}

.how-cheaper{
  padding: 4.75em 0 3.125em;
  &__slider{
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(3,calc((100% / 3) - 2em));
          gap: 3em;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.cheap-card{
  padding: 2.5em 0 2.5em 2.5em;
  background: linear-gradient(90deg, #EAEAF4 26.67%, #F7F7FC 100%);
  border-radius: .75em;
  display: grid;
  grid-template-columns: auto 8em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: .375em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.how-start{
  padding: 3.125em 0;
  &__inner{
    padding: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: .75em;
    background-color: $blockBg;
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__btn{
    flex-shrink: 0;
    width: 17.611em;
    height: 3.5em;
    margin-left: 1em;
    font-size: 1.125em;
    img{
      width: 1.33em;
      margin-right: .56em;
    }
  }
}

.result-steps{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: 100%;
}

.result-step{
  display: grid;
  grid-template-columns: 100%;
  gap: .25em;
  padding: 0 0 .875em 1.5em;
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: .375em;
    left: 0;
    width: .75em;
    height: .75em;
    border-radius: 50%;
    background-color: $primary;
  }
  &:before{
    content: '';
    position: absolute;
    top: 1.25em;
    height: calc(100% - 1.25em);
    width: 1px;
    border-left: 1px dashed $gray;
    left: .375em;
    transform: translateX(-50%);
  }
  &:last-child{
    &:before{
      display: none;
    }
  }
}

.how-start2{
  padding-top: 4.75em;
}


@media screen and (max-width: 576px){
  .calc-result{
    padding: 1em 0 3em;
    &__back{
      margin-right: 1.25em;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2.125em;
      gap: 1.5em;
    }
  }

  .result-card{
    padding: 1.5em;
    &__badge{
      padding: 1.25em;
      margin: 0 -1.5em -1.5em 0;
    }
    &__info{
      gap: .625em;
    }
    &__img{
      margin: .625em auto 0;
      max-width: 9.3125em;
    }
    &__text{
      margin-top: 1.25em;
    }
    &__items{
      margin-top: 1.25em;
    }
    &__item{
      &--bg{
        padding: .625em .3125em .625em .625em;
      }
    }
    &__price{
      gap: .625em;
      .display1{
        font-size: 1.9em;
      }
    }
    &__price2{
      margin-top: 1em;
      gap: 0;
    }
  }

  .calc-price{
    margin-top: 1.5em;
    position: relative;
    padding-bottom: 3.75em;
    gap: 1.25em;
    &__info{
      gap: 0;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      margin: 0;
    }
    &__btn{
      width: 100%;
    }
    &__info2{
      margin-top: 1.25em;
      gap: 0;
      ul{
        gap: 0;
      }
    }
    &__items{
      display: flex;
      gap: unset;
      align-items: stretch;
      width: calc(100% + 3em);
      margin-left: -1.5em;
      padding: 0 1.5em;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    &__item{
      width: 11.25em;
      flex-shrink: 0;
      padding: 1.5em;
      &:not(:last-child){
        margin-right: .75em;
      }
    }
    &__nav2{
      margin: 0;
    }
  }

  .how-cheaper{
    padding: 3em 0 1.875em;
    .swiper{
      &-slide{
        width: 19.5em;
        padding-right: .75em;
      }
    }
  }

  .cheap-card{
    padding: 1.5em 0 1.5em 1.5em;
    grid-template-columns: auto 6.625em;
    &__content{
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  .how-start{
    padding: 1.875em 0;
    &__inner{
      padding: 1.5em;
      display: block;
    }
    &__info{
      gap: .625em;
    }
    &__btn{
      margin: 1.2em 0 0 0;
      width: 100%;
    }
  }

  .result-steps{
    margin-top: 1.25em;
  }

  .result-step{
    gap: 0;
  }

  .how-start2{
    padding-top: 3em;
  }
}
