.doctor-nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: .875em;
    padding: .5em 0 .8em;
    position: relative;
    white-space: nowrap;
    &:not(:last-child){
      margin-right: 4.29em;
    }
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      height: .18em;
      width: 100%;
      border-radius: .5em .5em 0 0;
      background-color: $primary;
      display: block;
      opacity: 0;
      transition: all .3s
    }
    &._active,
    &:hover{
      color: $primary;
      &:before{
        opacity: 1;
      }
    }
  }
}

.doctor{
  &__wrap{
    background: url(../img/doctor-bg2.svg), radial-gradient(30.69% 104.62% at 22.99% 37.56%, #F8F7F7 0%, #E7E1D9 100%);
    background-size: cover;
    padding: 3em 0 0;
  }
  &__inner{
    display: grid;
    grid-template-columns: 45.1% auto;
    gap: 1em;
  }
  &__right{
    &>.container{
      @media screen and (min-width: 577px){
        padding: 0;
      }
    }
  }
  &__img{
    margin: 1.25em 0 0 -1em;
    position: relative;
    width: calc(100% + 1em);
    &>img{
      width: 100%;
      display: block;
      filter: drop-shadow(4.375em 1.5em 3.375em rgba(0, 0, 0, 0.1));
    }
    .doctor-card2__play{
      width: 5.625em;
      bottom: 8.563em;
      left: 50%;
      transform: translateX(-65%);
    }
  }
  &__name{
    font-size: 1em;
    display: grid;
    grid-template-columns: 100%;
    gap: .125em;
  }
  &__prof{
    margin-top: .125em;
  }
  &__specialisation{
    margin-top: 2.5em;
    &-title{
      color: rgba(128, 120, 122, 0.6);
    }
    &-items{

    }
  }
  &__pluses{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
  &__plus{
    display: flex;
    &-smile{
      flex-shrink: 0;
      margin-right: 10px;
      font-size: 1.5625em;
      line-height: 80%;
    }
  }
  &__address{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: .3125em;
  }
  &__nav{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__btn{
    img{
      width: 1.2em;
      margin-right: .5em;
    }
  }
  &__fixed{
    &-nav{
      position: fixed;
      z-index: 5;
      left: 0;
      bottom: 0;
      padding: .875em;
      width: 100%;
      background: rgba(255, 255, 255, 0.8);
      backdrop-filter: blur(7px);
      @media screen and (min-width: 577px){
        display: none;
      }
    }
    &-btn{
      width: 100%;
      img{
        width: 1.3em;
        margin-right: .6em;
      }
    }

  }
}

.about-doctor{
  padding: 5em 0 3.125em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
    margin-top: 2.5em;
  }
}

.hidden-text{
  height: 18em;
  overflow: hidden;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    height: 1.5em;
    width: 100%;
    background: linear-gradient(to bottom, #fff 0%, rgba(#fff, .1) 100%);
    left: 0;
    bottom: -.75em;
  }
  &._show{
    height: auto;
    &:before{
      opacity: 0;
    }
  }
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 3em;
    }
  }
}

.certificates{
  padding: 3.125em 0;
  .swiper{
    @media screen and (min-width: 577px){
      &-wrapper{
        grid-template-columns: repeat(5,calc(20% - (4em / 5)));
        gap: 1em;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}

.doctor-consult3{
  padding: 3.125em 0;
  .team-consult{
    margin: 0;
    align-items: center;
  }
}

.doctor-info{
  padding: 3.125em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(68.69% - 3em) 31.31%;
    gap: 3em;
  }
  .blog{
    background-color: transparent;
    @media screen and (min-width: 577px){
      padding: 0;
      &>.container{
        padding: 0;
      }
      .swiper{
        &-wrapper{
          grid-template-columns: 100%;
          gap: 1.25em;
        }
      }
      .blog-card{
        &__title{
          br{
            display: none;
          }
        }
        .author{
          display: none;
        }
      }
    }
  }
  .consult{
    @media screen and (min-width: 576px){
      padding: 0;
      &>.container{
        padding: 0;
      }
    }
  }
}

.consult{
  &__nav{
    margin-top: 2.5em;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
    margin-top: 2.5em;
  }
  &__more{
    margin-top: 2.5em;
  }
}

.blog--doctor{
  background-color: transparent;
}

.reviews{
  padding: 3.125em 0;
  &__inner{
    margin-top: 2.5em;
    max-width: 52.5em;
  }
  &__nav{
    margin-top: 2.5em;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
  }
  &__more{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 576px){
  .doctor-nav{
    overflow-x: auto;
    width: calc(100% + 3em);
    padding: 0 1.5em;
    margin-left: -1.5em;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    &__link{
      &:not(:last-child){
        margin-right: 1.45em;
      }
    }
  }

  .doctor {
    &__wrap {
      padding: 0;
      background-image: none;
      &>.container{
        padding: 0;
      }
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__left{
      background: url(../img/doctor-bg2.svg), radial-gradient(30.69% 104.62% at 22.99% 37.56%, #F8F7F7 0%, #E7E1D9 100%);
      background-size: cover;
      padding: 1em 1.5em 0;
    }
    &__right{
      padding: 1.5em 0 3em;
    }
    &__img{
      width: calc(100% + 3em);
      height: 16.875em;
      margin-left: -1.5em;
      overflow: hidden;
      .doctor-card2__play{
        bottom: 1.5625em;
        width: 3.75em;
      }
    }
    &__specialisation{
      margin-top: 1.5em;
    }
    &__pluses{
      gap: .8em;
    }
    &__address{
      margin-top: 1.5em;
    }
    &__nav{
      margin-top: 1.5em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__btn{
      img{
        width: 1.33em;
        margin-right: .6em;
      }
    }
    &__fixed-nav{
      &~.footer{
        margin-bottom: 4em;
      }
    }
  }

  .about-doctor{
    padding: 3em 0 1.875em;
    &__inner{
      margin-top: .625em;
      grid-template-columns: 100%;
      gap: 2.25em;
    }
  }

  .hidden-text{
    height: 23.5em;
    &:before{
      height: 2em;
    }
  }

  .certificates{
    padding: 1.875em 0;
    .swiper{
      &-slide{
        width: 12em;
        padding-right: .75em;
      }
    }
  }

  .doctor-consult3{
    padding: 1.875em 0;
  }

  .doctor-info{
    padding: 0;
    &>.container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .consult{
    padding: 1.875em 0;
    &__nav{
      margin-top: 1.5em;
    }
    &__items{
      margin-top: 1.5em;
      gap: 1.5em;
    }
    &__more{
      margin-top: 1.25em;
    }
  }

  .reviews{
    padding: 1.875em 0;
    &__inner{
      margin-top: 1.5em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__items{
      gap: 1.25em;
    }
  }
}
