.blog-page{
  padding-bottom: 3.125em;
  .filter{
    padding: 0;
    margin-top: 1.25em;
  }
  &__inner{
    margin-top: 3em;
    display: grid;
    grid-template-columns: calc(69.14% - 3em) 30.86%;
    gap: 3em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
  }
  &__more{
    margin-top: 3em;
  }
  @media screen and (min-width: 576px){
    .actual-articles{
      .container{
        padding: 0;
      }
      .swiper{
        &-wrapper{
          grid-template-columns: 100%;
          gap: 1.875em;
        }
      }
      .actual-card__groups{
        gap: 1.875em;
      }
    }
    .doctor-articles{
      .container{
        padding: 0;
      }
    }
  }
  .actual-articles{
    padding: 0;
    background-color: transparent;
  }
}

.recover{
  grid-column: 1/-1;
  margin: 3.375em 0 1.75em;
  padding: 3em;
  background-color: $blockBg;
  border-radius: .75em;
  overflow: hidden;
  &__slider{
    position: relative;
    margin-top: 2.5em;
    .swiper{
      &-container{
        position: relative;
        width: calc(100% + 1em);
      }
      &-slide{
        width: 25%;
        padding-right: 1em;
        opacity: 0;
        pointer-events: none;
        transition: all .3s;
        &-active,
        &-next,
        &-next + .swiper-slide,
        &-next + .swiper-slide + .swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
      &-button{
        &-prev{
          left: 0;
          transform: translateX(-50%);
        }
        &-next{
          right: 0;
          transform: translateX(50%);
        }
        &-disabled{
          opacity: 0 !important;
        }
      }
    }
  }

}

.recover-card,
.blog-item{
  &__props{
    display: flex;
    align-items: center;
    margin-top: .625em;
  }
  &__prop{
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-right: 1.25em;
    }
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .375em;
    }
  }
}

.recover-card{
  background-color: #fff;
  border-radius: .75em;
  &__top{
    position: relative;
    padding-bottom: 65.8%;
    img{
      border-radius: .75em .75em 0 0;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__content{
    padding: .875em;
  }
}

.doctor-articles{
  margin-bottom: 3.75em;
  &__desc{
    margin-top: 1.25em;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 2.5em;
  }
}

.article-card{
  display: flex;
  &__img{
    width: 2.5em;
    height: 2.5em;
    flex-shrink: 0;
    margin-right: .75em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: .5em;
    }
  }
}

.blog-item{
  &__wrap{
    background-size: cover !important;
    background-position: center !important;
    padding: 5em 0 2.5em;
  }
  &__badge{
    position: static;
    display: inline-flex;
    border-radius: .375em;
  }
  &__info{
    color: #fff;
    max-width: 58em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__bottom{
    margin-top: 5.625em;
  }
  &__day{
    margin-bottom: .25em;
  }
  &__props{
    margin-top: 1.25em;
  }
}

.article-info{
  padding: 2.5em 0;
  &__inner{
    display: grid;
    grid-template-columns: 60.545% 30.86%;
    justify-content: space-between;
    gap: 3em;
  }
}

.article-detail{
  padding: 2.25em 0 3.125em;
  &__wrap{
    width: 60.545%;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__item{
    display: flex;
  }
  &__num{
    flex-shrink: 0;
    width: 2.125em;
    height: 2.125em;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: .625em;
    background: #F2E8F9;
    color: $primary;
  }
  &__link{
    display: flex;
    align-items: center;
    margin-top: .5em;
    color: $primary;
    img{
      flex-shrink: 0;
      margin-left: .375em;
      width: .75em;
    }
  }
  .content-text{
    margin-top: 6.25em;
    hr{
      margin: 3.125em 0;
    }
    ol{
      margin-top: 3em;
    }
  }
}

.article-detail2{
  padding: 3.125em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(68.69% - 3em) 31.31%;
    gap: 3em;
  }
  &__text{
    padding: 2.5em;
    border-radius: .75em;
    background-color: $blockBg;
  }
  &__source{
    margin-top: 1.5em;
    &-link{
      color: $linkBlue;
    }
  }
}

.article-same{
  &__img{
    max-width: 11.25em;
    margin-top: .375em;
    img{
      width: 100%;
      display: block;
      border-radius: .75em;
    }
  }
  &__items{
    margin-top: .375em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__item{
    color: $linkBlue;
  }
}

.article-detail3{
  padding: 3.125em 0;
  .content-text{
    margin: 0;
  }
}

.article-stage{
  padding: 3.125em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(68.69% - 3em) 31.31%;
    gap: 3em;
  }
  .article-detail__wrap{
    width: 48.5em;
    max-width: 100%;
  }
  .content-text{
    margin: 0;
    ol{
      li{
        display: grid;
        grid-template-columns: 100%;
        gap: .375em;
        p{
          margin: 0 !important;
        }
        &:not(:last-child){
          margin-bottom: 1.5em;
        }
      }
    }
    .text16{
      line-height: 150%;
    }
    .text14{
      line-height: 171%;
    }
  }
  &__form{
    padding: 2.5em;
    background-color: $blockBg;
    border-radius: .75em;
    &-info{
      display: grid;
      grid-template-columns: 100%;
      gap: .625em;
    }
    &-grid{
      margin-top: 1em;
      display: grid;
      grid-template-columns: auto 3.625em;
      gap: .625em;
    }
    .fg{
      input{
        height: 3.3em;
      }
    }
    &-btn{
      height: 2.9007em;
      svg{
        width: 1.2em;
        height: auto;
      }
    }
  }
}

.article-advantages{
  padding: 3.125em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(68.69% - 3em) 31.31%;
    gap: 3em;
  }
  &__left{
    padding: 2.5em;
    background-color: $blockBg;
    border-radius: .75em;
  }
  &__info{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.125em));
    gap: 2.5em;
    margin-top: 3em;
    &-item{
      padding: 2.5em;
      background-color: #fff;
      border-radius: .75em;
    }
  }
  &__list{
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 1.25em;
    li{
      line-height: inherit;
      display: flex;
      &:before{
        content: '';
        width: 1.25em;
        height: 1.25em;
        flex-shrink: 0;
        background-image: url(../img/check4.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100% auto;
        margin-right: .625em;
        transform: translateY(0.1875em);
      }
    }
    &--reverse{
      li{
        &:before{
          background-image: url(../img/uncheck.svg);
        }
      }
    }
  }
}

.compare{
  padding: 3.125em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(68.69% - 3em) 31.31%;
    gap: 3em;
  }
  &__items{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__item{
    padding: 1.875em 2.5em;
    background-color: $blockBg;
    border-radius: .75em;
  }
  &__subtitle{
    padding-bottom: .91em;
    border-bottom: 1px solid $lightGray;
  }
  &__group{
    margin-top: 1.25em;
  }
}

.compare-info{
  padding-left: 2.1875em;
  &__text{
    margin-top: 2.5em;
  }
  .author{
    position: relative;
  }
  &__decor{
    position: absolute;
    top: 100%;
    left: -2.1875em;
    margin-top: .125em;
    width: 3.4375em;
  }
}

.article-table{
  padding: 3.125em 0;
  &__wrap{
    overflow-x: auto;
  }
  table{
    min-width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
    th,td{
      min-width: 16em;
      padding: .75em 1.25em;
    }
    th{
      height: 4em;
      background-color: $lightGray;
      font-weight: 400;
      text-align: left;
      &>*{
        font-size: 1em;
        line-height: 150%;
      }
    }
    td{
      height: 3em;
      &>*{
        font-size: .875em;
        line-height: 171%;
      }
    }
    tbody{
      tr:nth-child(even){
        td{
          background-color: $blockBg;
        }
      }
    }
  }
}

.article-price{
  padding: 3.125em 0 4.75em;
  &__wrap{
    max-width: 52.375em;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: .75em;
    margin-top: 3em;
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5em;
    background-color: $blockBg;
    border-radius: .75em;
  }
  .spoiler-card{
    margin-top: 6.25em;
  }
  &__source{
    ol{
      li{
        &:before{
          color: rgba(128, 120, 122, 0.6);
        }
      }
    }
  }
}

.recover-article{
  padding: 4.75em 0;
  margin: 0;
  .swiper-slide{
    width: calc(100% / 6);
    &-next {
      & + .swiper-slide + .swiper-slide + .swiper-slide,
      & + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.article-rating{
  padding: 4.75em 0 3.125em;
}

.blog--article{
  background-color: transparent;
}


@media screen and (max-width: 576px){
  .blog-page{
    overflow: hidden;
    padding-bottom: 1.875em;
    .container--mobp0{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
      margin-top: 2.125em;
      gap: 0;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__left{
      padding: 0 1.5em;
    }
  }

  .recover{
    margin: 1.5em 0 1.5em -1.5em;
    width: calc(100% + 3em);
    padding: 3em 1.5em;
    &__slider{
      margin-top: 1.5em;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 12em;
        padding-right: .75em;
        opacity: 1 !important;
        pointer-events: unset !important;
      }
      &-button{
        display: none;
      }
    }
  }

  .doctor-articles{
    padding: 2.5em 0;
    margin: 0;
    &__desc{
      margin-top: .625em;
    }
    &__items{
      margin-top: 1.5em;
      gap: 1.5em;
    }
  }

  .blog-item{
    &__wrap{
      padding: 2.25em 0 1.5em;
      background-size: auto 100% !important;
    }
    &__info{
      gap: .625em;
    }
    &__bottom{
      margin-top: 2.25em;
    }
    &__day{
      margin: 0;
    }
    &__props{
      margin-top: .625em;
    }
    &__prop{
      img{
        width: 1em;
      }
    }
  }

  .article-info{
    padding: 1.5em 0 1.125em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .article-detail {
    padding: 1.125em 0 1.875em;
    &__wrap {
      width: 100%;
    }
    .content-text{
      margin-top: 3.75em;
      hr{
        margin: 1.875em 0;
      }
    }
  }

  .article-detail2{
    padding: 1.875em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 2.25em;
    }
    &__text{
      padding: 1.5em;
    }
    &__source{
      margin-top: .875em;
      display: grid;
      grid-template-columns: 100%;
      gap: .875em;
    }
  }

  .article-detail3{
    padding: 1.875em 0;
  }

  .article-stage{
    padding: 1.875em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 3.75em;
    }
    &__right{
      order: -1;
    }
    &__form{
      padding: 1.5em;
      &-grid{
        margin-top: .875em;
      }
    }
    .content-text{
      h3{
        &:not(:last-child){
          margin-bottom: 1.21em;
        }
      }
    }
  }

  .article-advantages{
    margin: 1.125em 0;
    padding: 3em 0;
    background-color: $blockBg;
    &__inner{
      grid-template-columns: 100%;
    }
    &__left{
      padding: 0;
      background-color: transparent;
    }
    &__info{
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 1.5em;
      &-item{
        padding: 1.5em;
      }
    }
    &__list{
      margin-top: .625em;
      gap: .625em;
      li{
        &:before{
          width: 1.29em;
          height: 1.29em;
        }
      }
    }
  }

  .compare{
    padding: 1.875em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 3.75em;
    }
    &__items{
      display: flex;
      gap: unset;
      width: calc(100% + 3em);
      margin: 1.5em 0 0 -1.5em;
      padding: 0 0 0 1.5em;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    &__item{
      width: 15em;
      flex-shrink: 0;
      margin-right: .75em;
      padding: .875em;
    }
    &__subtitle{
      padding-bottom: .6em;
    }
    &__group{
      margin-top: .625em;
    }
  }

  .compare-info{
    padding-left: 1.875em;
    &__decor{
      left: -1.875em;
    }
    &__text{
      margin-top: 1.875em;
    }
  }

  .article-table{
    padding: 1.875em 0 1em;
    &__wrap{
      width: calc(100% + 3em);
      margin-left: -1.5em;
      padding: 0 1.5em .875em;
    }
    table{
      th,td{
        min-width: 13.8125em;
        &:nth-child(1){
          min-width: 11.375em;
        }
      }
      th{
        height: 3em;
        padding: .875em;
        &>*{
          font-size: .875em;
        }
      }
      td{
        height: 2.25em;
        padding: .5625em .875em;
        &>*{
          font-size: .75em;
        }
      }
    }
  }

  .article-price{
    padding: 1.875em 0 1.5em;
    &__wrap{
      max-width: 100%;
    }
    &__items{
      margin-top: 1.5em;
    }
    &__item{
      padding: .75em 1.25em;
    }
    .spoiler-card{
      margin-top: 2.25em;
    }
  }

  .recover-article{
    padding: 3em 0;
    margin: 0;
    width: 100%;
  }

  .article-rating{
    padding: 3em 0 1.875em;
  }
}
