.m-card{
  transition: all .3s;
  box-shadow: 0px .25em 1.5em rgba(119, 110, 112, 0.1);
  &:hover{
    box-shadow: 0px 0.0625em .25em rgba(119, 110, 112, 0.4);
  }
}

.advantage-card{
  padding: 2.5em;
  background: #FFFFFF;
  border-radius: .75em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.25em;
  }
  &__icon{
    flex-shrink: 0;
    margin-left: .375em;
    width: 2.125em;
    display: block;
  }
  &__desc{
    line-height: 150%;
    color: rgba(128, 120, 122, 0.6);
  }
}

.product-card{
  background: #FFFFFF;
  box-shadow: 0px 4px 20px rgba(119, 110, 112, 0.1);
  border-radius: .75em;
  overflow: hidden;
  &__top{
    padding: .5em .625em;
    width: calc(100% - 1em);
    background: $primary;
    backdrop-filter: blur(7px);
    border-radius: .75em 0 .75em 0;
    color: #fff;
  }
  &__hint{
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__img{
    display: flex;
    justify-content: center;
    padding: .875em;
    height: 10.25em;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__bottom{
    padding: .875em 1.5em 1.5em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    width: 100%;
    margin-bottom: 1.25em;
  }
  &__btn{
    margin-right: auto;
    font-size: 1em;
    padding: 0 1.25em;
    height: 3.125em;
    border-radius: .5em;
  }
}

.doctor-card2{
  display: grid;
  grid-template-columns: 58.17% auto;
  background: #FFFFFF;
  border-radius: .75em;
  height: 100%;
  overflow: hidden;
  &__img{
    width: 100%;
    max-height: 95%;
    object-fit: cover;
    display: block;
    border-radius: 0 0 0 .75em;
    &-wrap{
      position: relative;
      height: 100%;
      display: flex;
      align-items: flex-end;
      border-radius: 0 0 0 .75em;
      overflow: hidden;
    }
  }
  &__play{
    width: 3.75em;
    position: absolute;
    z-index: 2;
    bottom: 2.5em;
    left: 4.25em;
    transition: all .3s;
    opacity: .8;
    img{
      display: block;
      border-radius: 50%;
      width: 100%;
      //filter: drop-shadow(6px 14px 24px rgba(128, 120, 122, 0.2));
    }
    &:hover{
      opacity: 1;
    }
  }
  &__content{
    width: calc(100% + 3.66em);
    margin-left: -3.66em;
    padding: 1.875em .875em 1.875em 0;
    position: relative;
    z-index: 3;
  }
  &__name{
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
    font-size: 1em;
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: .875em;
  }
  &__feedback{
    margin-right: 1em;
    color: $linkBlue;
  }
  &__prof{
    margin-top: .875em;
    &-title{
      margin-bottom: .0625em;
      color: rgba(128, 120, 122, 0.6);
    }
  }
}

.history-card{
  position: relative;
  display: grid;
  grid-template-columns: auto 48.4%;
  background: $lightGray;
  border-radius: .75em;
  overflow: hidden;
  padding-top: 1em;
  &__img{
    align-self: flex-end;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding: 2.25em 1em 1.5em 1.5em;
    display: flex;
    flex-direction: column;
  }
  &__name{
    margin-bottom: 2.125em;
    font-size: 1em;
  }
  &__nav{
    margin-top: auto;
    display: flex;
  }
}

.rating-card{
  padding: 1.5em .875em;
  background: $blockBg;
  border-radius: 12px;
  text-align: center;
  &__img{
    display: flex;
    justify-content: center;
    height: 2.125em;
    img{
      object-fit: cover;
      max-width: 100%;
      height: 100%;
      display: block;
    }
  }
  &__num{
    margin-top: .75em;
    text-align: center;
    color: $primary;
  }
  &__site{
    color: rgba(128, 120, 122, 0.6);
  }
}

.reward-card{
  text-align: center;
  &__img{
    height: 8.625em;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../img/starts.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto 100%;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      width: 7.75em;
      height: .5em;
      top: 100%;
      left: 50%;
      transform: translate(-50%,.875em);
      background: radial-gradient(71.12% 25% at 50% 50%, #80787A 0%, rgba(128, 120, 122, 0) 100%);
      filter: blur(4.5px);
    }
    img{
      max-height: 100%;
      max-width: 100%;
      transform: translateY(.4em);
    }
  }
  &__title{
    margin-top: 2.25em;
    color: $primary;
  }
  &__info{
    margin-top: .375em;
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
  }
}

.equip-card{
  &__img{
    padding-bottom: 64.44%;
    position: relative;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: .75em;
    }
  }
  &__content{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
  }
  &__desc{
    color: rgba(128, 120, 122, 0.6);
  }
}

.blog-card{
  background: #fff;
  border-radius: .75em;
  overflow: hidden;
  height: 100%;
  &__top{
    padding-bottom: 59.64%;
    position: relative;
    background-size: cover;
    background-position: center;
  }
  &__info{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1.5em;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__badge{
    display: inline-flex;
    padding: .0625em .5em;
    background: $primary;
    backdrop-filter: blur(7px);
    border-radius: .375em;
    margin: 0 auto .625em 0;
  }
  &__title{
    width: 100%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    &:hover{
      color: #fff;
      opacity: .8;
    }
  }
  &__content{
    padding: 1.5em;
  }
  .author{
    margin-bottom: .875em;
  }
  &__props{
    display: flex;
    align-items: center;
  }
  &__prop{
    display: flex;
    align-items: center;
    color: rgba(128, 120, 122, 0.6);
    &:not(:last-child){
      margin-right: 1.875em;
    }
    img{
      flex-shrink: 0;
      width: 1em;
      margin-right: .375em;
    }
  }
}

.blog-card2{
  padding: 1.5em;
  background-color: #fff;
  border-radius: .75em;
  &__img{
    margin: 1.25em 0;
    position: relative;
    padding-bottom: 62.7%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      border-radius: .375em;
    }
  }
}

.selection-card{
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid rgba(255, 255, 255, 0.9);
  padding: 2.5em;
  border-radius: .75em;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  &__badge{
    display: none;
  }
  &__count{
    color: rgba(128, 120, 122, 0.6);
  }
  &__images{
    display: flex;
    align-items: center;
    margin: 1.25em 0;
  }
  &__img{
    width: 3.5em;
    height: 3.5em;
    border-radius: 50%;
    border: 2px solid #FFFFFF;
    filter: drop-shadow(0px 1px 11px rgba(0, 0, 0, 0.19));
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &:not(:first-child){
      margin-left: -1.5em;
    }
  }
  &__desc{
    margin-top: .25em;
  }
  &__nav{
    margin-top: auto;
    padding-top: .875em;
    display: flex;
  }
  &__link{
    svg{
      margin-left: .6em;
      width: 1.3em;
      height: auto;
    }
  }
}

.case-card{
  border-radius: .75em;
  background-color: #fff;
  overflow: hidden;
  position: relative;
  &__badge{
    z-index: 2;
    display: flex;
    align-items: center;
    top: 1em;
    left: 1.25em;
    border-radius: .375em;
    svg{
      width: .875em;
      height: auto;
      margin-right: .375em;
    }
  }
  &__top{
    position: relative;
    padding-bottom: 86%;
  }
  &__video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    &-img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: .75em .75em 0 0;
    }
    .doctor-card2__play{
      bottom: 50%;
      left: 50%;
      transform: translate(-50%,50%);
    }
  }
  &__images{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: repeat(2,calc(50% - 1px));
    gap: 2px;
    &._horizontal{
      grid-template-columns: repeat(2,calc(50% - 1px));
      grid-template-rows: 100%;
      img{
        &:first-child{
          border-radius: .75em 0 0 .75em;
        }
        &:last-child{
          border-radius:  0 .75em .75em 0;
        }
      }
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      &:first-child{
        border-radius: .75em .75em 0 0;
      }
      &:last-child{
        border-radius:  0 0 .75em .75em;
      }
    }
  }
  &__bottom{
    padding: 1.25em 1.5em 1.5em;
  }
}

.spoiler-card{
  border-top: .0625em solid $lightGray;
  &:last-child{
    border-bottom: .0625em solid $lightGray;
  }
  &__show{
    padding: 1.5em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    img{
      flex-shrink: 0;
      width: 1.25em;
      margin-left: 1em;
      transition: all .4s;
    }
    &._active{
      img{
        transform: rotate(-45deg);
      }
    }
  }
  &__hidden{
    padding-bottom: 1.5em;
    display: none;
  }
}

.edu-card{
  &__group{
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
    &:not(:last-child){
      margin-bottom: .875em;
    }
  }
}

.certificate-card{
  display: block;
  padding-bottom: 132%;
  position: relative;
  img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: .74em;
    filter: drop-shadow(0px .25em 1.25em rgba(119, 110, 112, 0.2));
  }
}

.consult-card{
  &:not(:last-child){
    padding-bottom: 3em;
    border-bottom: .0625em solid $lightGray;
  }
  &__title{
    line-height: 156%;
  }
  &__wrap{
    padding: 2.5em;
    background-color: $blockBg;
    border-radius: .75em;
    margin-top: 1.5em;
  }
  &__text{
    margin-top: 1.25em;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
    &>*{
      display: inline;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: .875em;
  }
  &__link,
  &__views{
    display: flex;
    align-items: center;
    svg{
      width: 1.35em;
      margin-right: .5em;
      height: auto;
    }
  }
}

.review-card{
  &:not(:last-child){
    padding-bottom: 2.5em;
    border-bottom: .0625em solid $lightGray;
  }
  &__author {
    .author {
      &__img {
        width: 4em;
        height: 4em;
        border-width: .16em;
        margin-right: .625em;
      }

      &__name {
        display: flex;
        align-items: center;

        img {
          width: 1.33em;
          margin-left: .556em;
        }
      }
    }
  }
  &__procedures{
    margin-top: 1.25em;
  }
  &__wrap{
    padding: 2.5em;
    background-color: $blockBg;
    border-radius: .75em;
    margin-top: 1.25em;
  }
  .hidden-text{
    height: 9em;
    &:before{
      bottom: -0.12em;
    }
  }
  &__video{
    margin-top: 1.25em;
    width: 20.75em;
    max-width: 100%;
    display: block;
    position: relative;
    &-img{
      width: 100%;
      border-radius: .75em;
    }
    .doctor-card2__play{
      bottom: 50%;
      left: 50%;
      transform: translate(-50%,50%);
    }
  }
  &__doctor{
    margin-top: 1.25em;
  }
}

.case-card2{
  display: grid;
  grid-template-columns: 41.11% 58.89%;
  &:not(:last-child){
    margin-bottom: 1em;
  }
  &__images{
    height: 100%;
    display: grid;
    grid-template-rows: repeat(2,calc(50% - 1px));
    gap: 2px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      &:first-child{
        border-radius: .75em 0 0 0;
      }
      &:last-child{
        border-radius:  0 0 0 .75em;
      }
    }
  }
  &__content{
    padding: .625em .875em;
  }
  &__title{
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__info{
    margin-top: .625em;
  }
}


@media screen and (max-width: 576px){
  .advantage-card{
    padding: 1.5em;
    &__top{
      margin-bottom: .625em;
    }
  }

  .product-card{
    &__top{
      padding: .25em .5em;
      width: calc(100% - 3em);
    }
    &__title{
      margin-bottom: 1.75em;
    }
    &__btn{
      font-size: .75em;
      padding: 0 1em;
      height: 2.8em;
      border-radius: .67em;
    }
  }

  .doctor-card2{
    grid-template-columns: 51.8% auto;
    &__content{
      width: calc(100% + 1.67em);
      margin-left: -1.67em;
      display: flex;
      flex-direction: column;
    }
    &__img{
      height: 12em;
    }
    &__name{
      gap: .125em;
    }
    &__info{
      order: 2;
      flex-direction: column;
      align-items: unset;
      margin-top: 0.5em;
    }
    &__prof{
      margin-top: .125em;
      &-title{
        display: none;
      }
      &-items{
        color: rgba(128, 120, 122, 0.6);
      }
    }
    &__play{
      bottom: 1em;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .history-card{
    grid-template-columns: auto 42%;
    padding: 0;
    &__content{
      padding: 2.125em 1.5em 1.5em;
    }
    &__img{
      height: 100%;
      display: flex;
      align-items: flex-end;
      img{
        width: 135%;
      }
    }
    &__name{
      margin-bottom: 2.5em;
    }
  }

  .rating-card{
    padding: .875em;
    &__img{
      height: 2.685em;
      align-items: center;
      img{
        width: 5em;
        height: auto;
        max-height: 100%;
      }
    }
    &__num{
      margin-top: .5em;
    }
  }

  .equip-card{
    &__img{
      padding-bottom: 69.5%;
    }
    &__content{
      margin-top: .625em;
      gap: 0;
    }
  }

  .blog-card{
    &__info{
      padding: .875em;
    }
    &__content{
      padding: 1.5em .875em;
    }
  }

  .blog-card2{
    &__img{
      margin: .875em 0;
      padding-bottom: 101.7%;
    }
  }

  .selection-card{
    padding: 1.5em;
    &__images{
      margin: .875em 0;
    }
  }

  .case-card{
    &__top{
      padding-bottom: 65%;
    }
    &__bottom{
      padding-top: .875em;
      padding-bottom: 1.25em;
    }
  }

  .consult-card{
    &:not(:last-child){
      padding-bottom: 1.25em;
    }
    &__title{
      line-height: 150%;
    }
    &__wrap{
      margin-top: .75em;
      padding: 1.5em;
    }
    &__text{
      -webkit-line-clamp:4;
      margin-top: .625em;
    }
  }

  .review-card{
    &:not(:last-child){
      padding-bottom: 1.5em;
    }
    &__author {
      .author {
        &__img {
          width: 2.875em;
          height: 2.875em;
        }
        &__name {
          img {
            width: .8em;
          }
        }
      }
    }
    &__procedures{
      margin-top: .75em;
    }
    &__wrap{
      margin-top: .75em;
      padding: .875em 1.5em;
    }
    .hidden-text{
      height: 18.5em;
    }
    &__video{
      max-width: 100%;
      width: 100%;
      margin-top: .75em;
    }
    &__doctor{
      margin-top: .75em;
    }
  }

  .case-card3{
    .case-card__top{
      padding-bottom: 96.2%;
    }
  }


}
