.review{
  padding-bottom: 2.25em;
  &__desc{
    margin-top: 1.25em;
    max-width: 48.5em;
    &-text{
      gap: 1em;
      display: grid;
      grid-template-columns: 100%;
      p{
        line-height: inherit;
      }
    }
  }
}

.reward--reviews{
  padding-bottom: 3.125em;
  .reward__title,
  .reward__desc{
    text-align: left;
  }
}

.history{
  padding-top: 3.125em;
}

.media--reviews{
  padding-bottom: 6.25em;
}

.review-modal{
  &__title,
  &__desc{
    text-align: center;
  }
  &__nav,
  &__rating{
    display: flex;
    justify-content: center;
    margin-top: 2.5em;
  }
}

.patient-history{
  background: url(../img/doctor-bg2.svg), radial-gradient(30.69% 104.62% at 22.99% 37.56%, #F8F7F7 0%, #E7E1D9 100%);
  background-size: cover;
  &__inner{
    display: grid;
    grid-template-columns: 41.72% 44.77%;
    justify-content: space-between;
      }
  &__img{
    margin-top: -3em;
    position: relative;
    z-index: 2;
    img{
      width: calc(100% + 4.625em);
      margin-left: -4.625em;
    }
  }
  &__left{
    padding-top: 1.5em;
  }
  &__right{
    padding-top: 10em;
  }
  &__info{
    padding-left: .625em;
    position: relative;
    display: inline-block;
  }
  &__name{
    span:before{
      display: none;
    }
  }
  &__decor{
    pointer-events: none;
    position: absolute;
    left: 100%;
    top: 0;
    transform: translate(-1em,-10em);
  }
  &__text{
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    p{
      line-height: inherit;
    }
  }
}

.patient-collection{
  padding: 6.25em 0 3.125em;
  &__slider{
    margin-top: 0;
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(3,calc(100% / 3 - 2em));
          gap: 3em;
          transform: unset !important;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.patient-card{
  padding-bottom: 151.5%;
  position: relative;
  display: block;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: .75em;
    filter: drop-shadow(0px .25em 1.25em rgba(119, 110, 112, 0.1));
    position: absolute;
    top: 0;
    left: 0;
  }
}


@media screen and (max-width: 576px){
  .review{
    padding-bottom: 1.875em;
  }

  .reward--reviews{
    padding-bottom: 1.875em;
  }

  .history--reviews{
    padding-top: 1.875em;
  }

  .media--reviews{
    padding-bottom: 3.75em;
  }

  .patient-history{
    background: unset;
    padding-bottom: 1.875em;
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__left{
      background: url(../img/doctor-bg2.svg), radial-gradient(30.69% 104.62% at 22.99% 37.56%, #F8F7F7 0%, #E7E1D9 100%);
      background-size: cover;
      padding: 1.5em 1.5em 0;
    }
    &__decor{
      display: none;
    }
    &__img{
      margin-top: 1em;
      img{
        width: calc(100% + 1.5em);
        margin-left: -1.5em;
      }
    }
    &__name{
      span:before{
        display: unset;
      }
    }
    &__right{
      padding: 5em 1.5em 0;
    }
    &__text{
      position: relative;
      padding-left: 1.875em;
      gap: .5em;
      &:before{
        content: '';
        position: absolute;
        top: -.5em;
        left: .875em;
        width: .875em;
        height: .875em;
        background-image: url(../img/brand-icon.svg);
        background-size: 100% auto;
        background-repeat: no-repeat;
      }
    }
  }

  .patient-collection{
    padding: 1.875em 0;
    .swiper{
      &-slide{
        width: 18.23em;
        padding-right: .75em;
      }
    }
  }
}
