.select-doctor{
  &__desc{
    margin-top: 1.25em;
    max-width: 48.4em;
  }
}

.quiz{
  margin-top: 3em;
  padding: 2.5em;
  background-color: $black;
  border-radius: .75em;
  max-width: 52.32em;
  color: $lightGray;
  &__progress{
    background-color: $gray;
    height: .1875em;
    border-radius: 0 1.875em 1.875em 0;
    overflow: hidden;
    position: relative;
    &-fill{
      width: 20%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: $primary;
      transition: all .3s;
    }
  }
  &__step{
    margin-top: 1.25em;
    display: none;
    &._active{
      display: block;
    }
  }
  &__desc{
    margin-top: .375em;
  }
  &__radios{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 1.875em;
    gap: 1em;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.25em;
    &._hidden{
      display: none;
    }
  }
  &__select{
    margin-top: .625em;
  }
  &__prev,
  &__next{
    font-size: .75em;
    border-radius: .75em;
    height: 2.84em;
    img{
      width: 1.5em;
      &:nth-child(1){
        margin-right: .75em;
      }
      &:nth-child(2){
        margin-left: .75em;
      }
    }
    &._hidden{
      opacity: 0;
      pointer-events: none;
    }
    &._disabled{
      opacity: .2;
      pointer-events: none;
    }
  }
  &__finish-nav{
    display: flex;
    margin-top: 1.25em;
    img{
      margin-right: .5em;
    }
  }
  &__link{
    width: 100%;
  }
}

.selection{
  padding: 6.25em 0 4.625em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;
    ._best{
      grid-column: 1/3;
      order: -1;
      border-color: $primary;
      .selection-card__link{
        background-color: $primary;
        color: $lightGray;
        path{
          fill: $lightGray;
        }
      }
      .selection-card__badge{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .select-doctor{
    &__desc{
      margin-top: .625em;
    }
  }

  .quiz{
    margin-top: 1.5em;
    padding: 1.25em;
    &__step{
      margin-top: .625em;
    }
    &__radios{
      margin-top: 20px;
    }
    &__radio{
      label{
        font-size: .75em;
        align-items: center;
        &:before{
          width: 2em;
          height: 2em;
          margin-right: .8em;
        }
      }
    }
  }

  .selection{
    padding: 2.5em 0 1.825em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
      ._best{
        grid-column: 1/2;
        padding-top: 2.125em;
      }
    }
  }
}
