.questionnaire{
  padding-bottom: 5em;
  &__wrap{
    width: 100%;
    max-width: 40em;
    background: #FFFFFF;
    box-shadow: 0 .25em 5em rgba(119, 110, 112, 0.3);
    border-radius: .75em;
    padding: 2.5em;
    margin: 0 auto;
  }
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: 2.5em;
    &-back{
      flex-shrink: 0;
      width: 1.75em;
      height: 1.75em;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        width: .75em;
      }
    }
  }
  &__progress{
    flex-grow: 1;
    height: 1px;
    background-color: $lightGray;
    position: relative;
    &-fill{
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      background-color: $gray;
    }
    &-round{
      position: absolute;
      top: 50%;
      transform: translate(-50%,-50%);
      width: .5625em;
      height: .5625em;
      border-radius: 50%;
      background-color: #fff;
      border: 1px solid $lightGray;
      &._active{
        background-color: $primary;
        border-color: $primary;
      }
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2.5em;
  }
  &__prev,
  &__next{
    img{
      width: 1.125em;
      &:nth-child(1){
        margin-right: .5em;
      }
      &:nth-child(2){
        margin-left: .5em;
      }
    }
    &._hidden{
      opacity: 0;
      pointer-events: none;
    }
    &._disabled{
      opacity: .2;
      pointer-events: none;
    }
  }
  &__prev{
    &._disabled{
      opacity: 0.2;
      pointer-events: none;
    }
  }
}

.step{
  &__title{
    line-height: 121%;
  }
  &__info{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__form{
    margin-top: 2.5em;
  }
  &__fields{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    .fg{
      &--date{
        max-width: 16.375em;
      }
    }
  }
  &__check1,
  &__check3{
    margin-top: 2.5em;
  }
  &__check1{
    label{
      &>*{
        font-size: .67em;
      }
    }
  }
  &__hint{
    margin-top: 1.25em;
  }
  &__img{
    padding: .59375em;
    height: 17.3125em;
    background: #FAFAFA;
    border-radius: .875em;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__question{
    margin-top: 1.25em;
    &-radios{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1em;
      margin-top: 1.25em
    }
  }
  &__select{
    margin-top: 1.25em;
  }
  &__group{
    margin-top: 2.5em;
  }
  &__checks{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__variants{
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__radio{
    label{
      &:before{
        width: 1.4em;
        height: 1.4em;
      }
    }
  }
  &__example{
    margin-top: 1.5em;
    display: flex;
    &-btn{
      font-size: 1em;
      display: flex;
      img{
        width: 1.5em;
        flex-shrink: 0;
        display: block;
      }
    }
  }
}

.step-success{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    width: 2.75em;
    margin-bottom: 1.875em;
  }
  &__desc{
    margin: 1.25em 0 1.875em;
  }
  &__back{
    img{
      width: 1.32em;
      margin-right: .4em;
    }
  }
  .order-product{
    margin: 0 0 1.875em;
    text-align: left;
  }
}

.teeth{
  &__items{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc((100% / 3) - (2em / 3)));
    gap: 1em;
  }
}

.teeth-card,
.doctor-card3{
  position: relative;
  &__radio{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    z-index: 2;
    opacity: 0;
    &:checked{
      &~.teeth-card__inner,
      &~.doctor-card3__inner{
        background: rgba(179, 111, 222, 0.1);
        border-color: $primary;
        color: $primary;
        &:before{
          opacity: 1;
        }
        .teeth-card__desc{
          color: $primary;
        }
      }
    }
  }
  &__inner{
    height: 100%;
    padding: 1.25em;
    border: 1px solid $lightGray;
    border-radius: .5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 2px solid $primary;
      border-radius: inherit;
      opacity: 0;
      pointer-events: none;
    }
  }
  &__img{
    position: relative;
    padding-bottom: 58.8%;
    margin-bottom: 1.25em;
    img{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
  }
}

.doctor-card3{
  &__price{
    margin-top: .625em;
  }
}

.uploaded{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: 100%;
  gap: .625em;
}

.uploaded-card{
  display: grid;
  grid-template-columns: 4.75em auto;
  gap: 1.875em;
  align-items: center;
  border: 1px solid $lightGray;
  border-radius: .75em;
  padding: 1.5em;
  &--red{
    background: #FBF3F3;
    border-color: #D79090;
  }
  &__img{
    img{
      display: block;
      width: 100%;
      border-radius: .5em;
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: .875em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    display: flex;
    align-items: center;
    font-size: 1em;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .375em;
    }
  }
}

.upload{
  &__buttons{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }
  &__btn{
    font-size: 1em;
    padding: 1.875em;
    border-radius: .75em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    &-round{
      background-color: $lightGray;
      border-radius: 50%;
      width: 2.75em;
      height: 2.75em;
      display: flex;
      align-items: center;
      justify-content: center;
      backdrop-filter: blur(.4375em);
      margin-bottom: .675em;
      img{
        width: 1.25em;
        height: 1.25em;
      }
    }
  }
}

.order-product{
  width: 100%;
  margin-top: 2.5em;
  padding: 1.5em;
  border: 1px solid $lightGray;
  border-radius: .5em;
  &__price{
    margin-top: .5em;
  }
}

.pay-card{
  margin-top: 2.5em;
  padding: 1.5em;
  background-color: $blockBg;
  border-radius: .75em;
  max-width: 25.8125em;
  &__img{
    width: 4.4375em;
    height: 4.4375em;
    background: #E7E4E0;
    border-radius: 50%;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
  &__grid{
    margin-top: .875em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: .875em 1.25em;
  }
  &__field{
    &--full{
      grid-column: 1/-1;
    }
    input{
      width: 100%;
      height: 2.125em;
      border: none;
      font-size: 1em;
      padding: 0 .875em;
      background: rgba(255, 255, 255, 0.8);
      border-radius: .5em;
      &::placeholder{
        color: rgba(128, 120, 122, 0.6);
      }
      &:focus{
        background: #F2E8F9;
      }
    }
  }
}

@media screen and (max-width: 576px){
  .questionnaire{
    padding: 1em 0 1.875em;
    &__wrap{
      padding: 0;
      background-color: transparent;
      box-shadow: none;
    }
    &__top{
      margin-bottom: 1.25em;
    }
  }

  .step{
    &__info{
      margin-top: 2.25em;
      gap: .5em;
    }
    &__num{
      margin-bottom: .25em;
    }
    &__form{
      margin-top: 1.5em;
    }
    &__fields{
      .fg{
        margin-bottom: .25em;
        &--date{
          max-width: 100%;
        }
      }
    }
    &__check1{
      margin-top: 1.25em;
      label{
        &>*{
          font-size: .75em;
          line-height: 150%;
        }
      }
    }
    &__question{
      margin-top: 1.5em;
      &-radios{
        margin-top: 1.5em;
      }
    }
    &__group{
      margin-top: 3em;
    }
    &__checks{
      margin-top: 1.5em;
    }
    &__variants{
      gap: 1.5em;
    }
  }

  .step-success{
    &__icon{
      margin-bottom: 1.5em;
    }
    &__desc{
      margin: .625em 0 1.5em;
    }
    &__back{
      img{
        width: 1.5em;
        margin-right: .67em;
      }
    }
    .order-product{
      margin-bottom: 1.5em;
    }
  }

  .teeth{
    &__items{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
  }

  .teeth-card,
  .doctor-card3{
    &__inner{
      padding: .625em;
    }
    &__img{
      margin-bottom: .5em;
    }
    &__info{
      gap: 0;
    }
  }

  .doctor-card3{
    &__price{
      margin-top: .5em;
    }
  }

  .uploaded{
    margin-top: 3em;
    gap: .75em;
  }

  .uploaded-card{
    padding: .625em .5em .625em .625em;
    grid-template-columns: 3.25em auto;
    gap: 1.25em;
    &__info{
      gap: .5em;
    }
  }

  .upload{
    &__btn{
      padding: 1em;
      &-round{
        width: 2.125em;
        height: 2.125em;
      }
      img{
        width: .89em;
        height: .89em;
      }
    }
  }

  .order-product{
    margin-top: 1.5em;
  }

  .pay-card{
    margin-top: 4.5em;
    padding: .875em;
    &__img{
      width: 2.75em;
      height: 2.75em;
    }
  }
}
