input[type="date"] {
  position: relative;
  background-image: url(../img/calendar4.svg);
  background-position: left 1.25em center;
  background-repeat: no-repeat;
  background-size: 1.125em auto;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
  color: transparent;
  background: transparent;
  opacity: 0;
}

input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-clear-button {
  z-index: 1;
}

input[type="date"]::before {
  content: attr(data-placeholder);
  width: 100%;
  color: #5e6162;
}

input[type="date"]:focus::before,
input[type="date"]:valid::before {
  display: none
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    transition: all .3s;
    path{
      fill: $lightGray;
    }
    &:not(:first-child) {
      margin-right: 6px;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $primary;
      }
      &~.rating-input__item {
        path{
          fill: $primary;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"] .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"] .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"] .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"] .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"] .rating-input__item:nth-child(n+1) {
  path{
    fill: $primary;
  }
}

