.m-check,
.m-radio{
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:hover {
      & + label {
        &:before {
          border-color: #BAB4B1;
          background-color: #BAB4B1;
          box-shadow: 0px 0px 4px rgba(57, 51, 53, 0.3);
        }
      }
      &:checked+label{
        &:before{
          border-color: $primary;
          background-color: $primary;
          box-shadow: 0px 0px 4px rgba(57, 51, 53, 0.3);
        }
      }
    }
  }
  label {
    font-size: 1.125em;
    line-height: 156%;
    display: flex;
    cursor: pointer;
    &:before {
      content: '';
      border-radius: .25em;
      background-color: $gray;
      border: 1px solid $black;
      width: 1.5em;
      height: 1.5em;
      flex-shrink: 0;
      margin-right: .625em;
      transition: all .100s;
      background-size: 100% auto;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  &--white{
    label{
      &:before{
        background-color: #FDFDFD;
        border-color: $lightGray;
      }
    }
    input{
      &:hover {
        & + label {
          &:before {
            border-color: $primary;
            background-color: #FDFDFD;
          }
        }
        &:checked+label{
          &:before{
            border-color: $lightGray;
            background-color: #FDFDFD;
          }
        }
      }
    }
  }
}

.m-check{
  input{
    &:checked+label {
      &::before {
        border-color: $black;
        background-color: $primary;
        background-image: url(../img/check2.svg);
      }
    }
  }
  &--white{
    input{
      &:checked+label {
        &::before {
          border-color: $lightGray;
          background-color:#FDFDFD;
          background-image: url(../img/check5.svg);
        }
      }
    }
  }
}

.m-radio{
  input{
    &:checked+label {
      &::before {
        border-color: $black;
        background-color: $primary;
        background-image: url(../img/radio.svg);
      }
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-size: 45% auto;
    }
  }
  &--white{
    input{
      &:checked+label {
        &::before {
          border-color: $lightGray;
          background-color:#FDFDFD;
          background-image: url(../img/radio2.svg);
        }
      }
    }
  }
}

.m-switch{
  position: relative;
  input{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    &:checked+label{
      background-color: $primary;
      &:before{
        left: 1.27em;
      }
    }
  }
  label{
    display: block;
    width: 2.62em;
    height: 1.5em;
    background: $gray;
    border-radius: 3.125em;
    position: relative;
    font-size: 1em;
    line-height: 150%;
    &:before{
      content: '';
      position: absolute;
      width: 1.2em;
      height: 1.2em;
      border-radius: 50%;
      background-color: #fff;
      top: 50%;
      transform: translateY(-50%);
      left: .15em;
      transition: all .2s;
    }
  }
}


@media screen and (max-width: 576px){
  .m-check{
    label{
      font-size: 1em;
    }
  }

  .fg{
    font-size: 1em;
    .m-select .current{
      font-size: 1em;
      padding: 0 .875em;
    }
    .m-select{
      height: 3.5em;
      .current{
        background-size: .9em;
      }
    }
  }

  .rating-input-mob{
    .rating-input__item{
      width: 1.35em;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
}
