@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
[data-link-href]{
  cursor: pointer;
}

.sprite-black{
  fill: $black;
}

.sprite-primary{
  fill: $primary;
}

.icon18{
  width: 1.126em;
  height: 1.126em;
}

.icon16{
  width: 1em;
  height: 1em;
}

.icon12{
  width: 0.751em;
  height: 0.751em;
}

.swiper{
  &-button{
    width: 3.75em;
    height: 3.75em;
    background: rgba(255, 255, 255, 0.6);
    box-shadow: 6px 14px 24px rgba(128, 120, 122, 0.2);
    backdrop-filter: blur(7px);
    border-radius: 50%;
    &:hover{
      box-shadow: 0px 1px 4px rgba(128, 120, 122, 0.4);
    }
    img{
      width: 1.5em;
      display: block;
    }
  }
  &-pagination{
    display: flex;
    align-items: center;
    justify-content: center;
    position: static;
    margin-top: 1.5em;
    &-bullet{
      width: .5em;
      height: .5em;
      background-color: $lightGray;
      opacity: 1;
      margin: 0 .3125em !important;
      &-active{
        background-color: $primary;
      }
    }
  }
}

.m-badge{
  position: absolute;
  top: 0;
  left: 0;
  backdrop-filter: blur(7px);
  border-radius: .75em 0 .75em 0;
  background-color: $primary;
  color: #fff;
  padding: .19em .5em;
}

.author{
  display: flex;
  align-items: center;
  &__img{
    margin-right: 1.25em;
    flex-shrink: 0;
    width: 3.125em;
    height: 3.125em;
    overflow: hidden;
    border: .0625em solid #fff;
    border-radius: 50%;
    img{
      filter: drop-shadow(0px 1px 11px rgba(0, 0, 0, 0.19));
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
}

.tags{
  margin-top: -.32em;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.tag{
  display: flex;
  align-items: center;
  height: 2.5em;
  margin: .835em .72em 0 0;
  padding: 0 .858em;
  border: .1em solid $gray;
  border-radius: .45em;
  &:hover{
    background-color: $gray;
    color: $lightGray;
  }
}

.tabs{
  display: flex;
  align-items: center;
  &__wrap{
    display: flex;
    align-items: center;
    border-bottom: 1px solid $lightGray;
  }
}

.tab{
  font-size: 1em;
  line-height: 150%;
  padding: .75em 0;
  position: relative;
  cursor: pointer;
  &:not(:last-child){
    margin-right: 2.5em;
  }
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    width: 100%;
    border-radius: .5em .5em 0 0;
    background-color: $primary;
    display: block;
    opacity: 0;
    transition: all .3s
  }
  &._active,
  &:hover{
    &:before{
      opacity: 1;
    }
  }
  &._active{
    pointer-events: none;
  }
}



@media screen and (max-width: 576px){
  .author{
    &__img{
      width: 2.25em;
      height: 2.25em;
      margin-right: .625em;
    }
  }

  .tags{
    width: calc(100% + .15em);
  }

  .tag{
    margin-right: .72em;
    padding: 0 .67em;
  }

  .tab{
    padding: .625em 0;
    span{
      font-size: .875em;
    }
    &:not(:last-child){
      margin-right: 1.25em;
    }
  }
}
