@import "../mixins";

.tv{
  background-color: $blockBg;
  background-image: url(../img/tv-bg.svg);
  background-repeat: no-repeat;
  background-position: right 2.3125em top 0;
  background-size: 48.3125em auto;
  margin-bottom: 3.125em;
  &__inner{
    padding: 1em 0 6.25em;
  }
  &__tabs{
    margin-top: 1.5em;
  }
  &__info{
    margin-top: 5.125em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    max-width: 52.25em;
  }
  .tv-med__list{
    margin-top: .67em;
  }
  &__nav{
    display: flex;
    margin-top: 4.75em;
  }
  &__btn{
    width: unset;
    img{
      width: 1.2em;
      margin-right: .5em;
      display: block;
      flex-shrink: 0;
    }
  }
}

.tv2{
  background-image: url(../img/tv-bg2.svg);
}

.tabs2{
  display: flex;
  align-items: center;
}

.tab2{
  height: 2.5625em;
  padding: 0 1em;
  border: 1px solid $gray;
  border-radius: .5em;
  display: flex;
  align-items: center;
  justify-content: center;
  &:not(:last-child){
    margin-right: 1.25em;
  }
  span{
    font-size: .875em;
  }
  &._active{
    background-color: $primary;
    border-color: $primary;
    color: #FFFFFF;
    pointer-events: none;
  }
}

.tv-nums{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2em));
  gap: 3em;
  margin-top: 4.75em;
  &__item{
    padding: 2.5em;
    background: #FFFFFF;
    border: 1px solid $lightGray;
    border-radius: .75em;
  }
  &__text{
    display: grid;
    grid-template-columns: 100%;
    gap: .5em;
  }
}

.help{
  padding: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: calc(69.2% - 3em) 30.8%;
    gap: 3em;
  }
  &__items{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .75em));
    gap: 1em;
  }
  &__right{
    padding: 2.5em;
    background-color: $blockBg;
    border-radius: .75em;
  }
  &__text{
    margin-top: 1.875em;
    ul{
      gap: .625em;
      li{
        &:before{
          background-color: $primary;
        }
      }
    }
  }
}

.help-card{
  padding: 1.5em 1.125em;
  border: 1px solid $lightGray;
  border-radius: .75em;
  text-align: center;
  &__icon{
    height: 2.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: .875em;
    img{
      height: 100%;
    }
  }
}

.tariffs{
  padding: 3.125em 0;
  &__slider{
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(3,calc((100% / 3) - 2em));
          gap: 3em;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.tariff-card{
  padding: 2.5em;
  background-color: $blockBg;
  display: flex;
  flex-direction: column;
  border-radius: .75em;
  &__info{
    padding-bottom: 1.25em;
    border-bottom: 1px solid $lightGray;
    display: grid;
    grid-template-columns: 100%;
    gap: .5em;
    min-height: 6.2em;
    margin-bottom: 1.25em;
  }
  &__title{
    font-size: 1.5em;
    line-height: 120%;
    font-weight: 400;
  }
  &__desc{
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
    line-height: 143%;
  }
  &__list{
    gap: .625em;
    li{
      &:before{
        width: 1.3em;
        height: 1.3em;
        transform: translateY(.15em);
      }
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.25em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    font-size: .875em;
    height: 2.86em;
    padding: 0 1em;
  }
}

.how-consult{
  padding: 3.125em 0;
  .tv-md2__steps{
    padding: 2.5em;
    background: radial-gradient(62.23% 670.3% at 91.8% 13.33%, #E8E2DA 0%, #E7E1D9 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    box-shadow: 0px .25em 1.25em rgba(119, 110, 112, 0.1);
    border-radius: .75em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
    margin-top: 2.5em;
    @media screen and (min-width: 577px){
      grid-auto-flow: column;
      grid-template-columns: repeat(2,1fr);
      grid-template-rows: repeat(3,auto);
    }
    .tv-med2__step{
      margin: 0;
    }
    .tv-med2__step-top{
      min-height: unset;
    }
  }
  &__time{
    margin-top: .375em;
    display: flex;
    align-items: center;
    img{
      width: .875em;
      display: block;
      flex-shrink: 0;
      margin-right: .375em;
    }
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
  }
  &__btn{
    font-size: .875em;
    height: 2.86em;
    padding: 0 1em;
  }
}

.reviews2{
  &__grid{
    display: grid;
    grid-template-columns: calc(69.2% - 3em) 30.8%;
    gap: 3em;
  }
  .reviews__items{
    margin-top: 3em;
  }
  &__links{
    display: grid;
    grid-template-columns: 100%;
    gap: .375em;
    margin-top: 3em;
  }
  &__link{
    color: $linkBlue;
    font-size: .875em;
    line-height: 171%;
    &:hover{
      color: $black;
    }
  }
}

.review-about{
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 3em;
    margin-top: 2.5em;
  }
}

.about-card{
  &__icon{
    height: 2.875em;
    margin-bottom: .75em;
    img{
      height: 100%;
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
  }
}

.tv-services{
  margin-top: 5em;
  display: grid;
  grid-template-columns: repeat(3,calc((100% / 3) - 2em));
  gap: 3em;
}

.tv-service-card{
  position: relative;
  padding: 2.5em;
  background-color: #fff;
  border-radius: .75em;
  display: flex;
  flex-direction: column;
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__nav{
    padding-top: 2.5em;
    display: flex;
    margin-top: auto;
  }
}

.doctors-trust{
  padding: 3.125em 0;
  .tv-nums{
    margin-top: 2.5em;
  }
}

.doctors-help{
  padding: 3.125em 0;
  &__inner{
    padding: 0 2.5em;
    background: radial-gradient(62.23% 670.3% at 91.8% 13.33%, #E8E2DA 0%, #E7E1D9 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    box-shadow: 0px .25em 1.25em rgba(119, 110, 112, 0.1);
    border-radius: .75em;
    display: grid;
    align-items: center;
    grid-template-columns: 26.25em calc(100% - 29.25em);
    gap: 3em;
  }
  &__img{
    img{
      width: 100%;
    }
  }
  &__content{
    padding: 2em 0;
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    max-width: 36em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    height: 3.4em;
  }
}

.tv-text{
  margin-top: 6.25em;
  &__title{
    margin-bottom: 1.25em;
  }
  .content-text{
    &:not(:last-child){
      margin-bottom: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .tv{
    background-image: none;
    margin-bottom: 1.875em;
    &__inner{
      padding-bottom: 3.75em;
    }
    &__info{
      margin-top: 3em;
      gap: .625em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      width: 100%;
    }
  }

  .tv-nums{
    overflow-x: auto;
    width: calc(100% + 3em);
    padding: 0 1.5em;
    margin: 3em 0 0 -1.5em;
    display: flex;
    gap: unset;
    @include scroll0;
    &__item{
      flex-shrink: 0;
      width: 11.25em;
      padding: 1.25em .875em;
      &:not(:last-child){
        margin-right: .75em;
      }
    }
    &__text{
      gap: .25em;
    }
  }

  .tab2{
    height: 2.0625em;
    padding: 0 .75em;
    &:not(:last-child){
      margin-right: .75em;
    }
    span{
      font-size: .75em;
    }
  }

  .help{
    padding: 1.875em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 3.75em;
    }
    &__right{
      padding: 1.5em;
    }
    &__items{
      overflow-x: auto;
      width: calc(100% + 3em);
      padding: 0 1.5em;
      margin: 1.5em 0 0 -1.5em;
      display: flex;
      gap: unset;
      @include scroll0;
    }
    &__text{
      margin-top: 1.43em;
    }
  }

  .help-card{
    width: 9.0625em;
    flex-shrink: 0;
    padding: 1.25em .875em;
    &:not(:last-child){
      margin-right: .75em;
    }
  }

  .tariffs{
    padding: 1.825em 0;
    .swiper{
      &-slide{
        width: 19.5em;
        padding-right: .75em;
      }
    }
  }

  .tariff-card{
    padding: 1.5em;
    &__bottom{
      gap: 2.5em;
      padding-top: 2.5em;
    }
  }

  .how-consult{
    padding: 1.825em 0;
    .tv-md2__steps{
      padding: 1.5em;
    }
    &__grid{
      gap: 1.25em;
      margin-top: 1.25em;
      grid-template-columns: 100%;
      .tv-med2__step-num{
        background-color: $primary;
        color: #fff;
      }
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }

  .reviews2{
    &__grid{
      grid-template-columns: 100%;
    }
    &__right{
      order: -1;
    }
    .reviews__items{
      margin-top: 1.5em;
    }
    &__link{
      font-size: .75em;
      line-height: 150%;
    }
  }

  .review-about{
    &__items{
      margin-top: 1.5em;
      gap: 1.5em;
    }
  }

  .about-card{
    &__icon{
      margin-bottom: .625em;
    }
  }

  .tv-services{
    margin-top: 3em;
    gap: 1.5em;
    grid-template-columns: 100%;
  }

  .tv-service-card{
    padding: 2.5em 1.5em 1.5em;
    &__info{
      gap: .5em;
    }
    &__nav{
      padding-top: 1.25em;
    }
  }

  .doctors-trust{
    padding: 1.825em 0;
    .tv-nums{
      margin-top: 1.5em;
    }
  }

  .doctors-help{
    padding: 1.825em 0;
    &__inner{
      padding: 1.825em 1.5em 0 0;
      grid-template-columns: 9.0625em calc(100% - 9.0625em);
      gap: 0;
    }
    &__img{
      padding-bottom: 136%;
      position: relative;
      img{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        object-fit: cover;
      }
    }
    &__content{
      padding: 0;
      width: calc(100% + 1em);
      position: relative;
      z-index: 2;
      margin-left: -1em;
    }
    &__info{
      gap: .125em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
      height: 2.75em;
      padding: 0;
      font-size: .75em;
    }
  }

  .tv-text{
    margin-top: 1.5em;
    &__title{
      margin-bottom: .86em;
    }
  }
}
