.team{
  padding-bottom: 6.25em;
  background-size: cover;
  background-position: right center;
  &__wrap{
    @media screen and (min-width: 577px){
      background-image: none !important;
    }
  }
  &__text{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 6em;
  }
  &__info{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 2.5em;
    &-content{
      margin-left: 1.125em;
    }
    &-title{
      color: rgba(128, 120, 122, 0.6);
    }
    &-list{
      margin-top: .125em;
      width: 100%;
      color: rgba(128, 120, 122, 0.6);
    }
  }
  .selection-card__images{
    margin-top: 0;
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    margin-top: 5.625em;
  }
  &__right{
    flex-shrink: 0;
  }
}

.for-who{
  &__list{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 3.125em;
    display: flex;
  }
  &__btn{
    height: 3.3em;
    padding: 0 2.875em;
    img{
      width: 1.2em;
      margin-right: 0.5em;
    }
  }
  &__text{
    display: none;
  }
}

.why-team{
  background: #FFFFFF;
  box-shadow: 0px .25em 1.25em rgba(119, 110, 112, 0.1);
  border-radius: .75em;
  padding: 1.5em 2.5em 2.5em;
  width: 25em;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    width: 7.2em;
  }
  &__title{
    max-width: 10em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
  }
}

.team-modal{
  .modal__dialog{
    width: 50em;
  }
  &__desc{
    margin-top: .6em;
    display: grid;
    grid-template-columns: 100%;
    gap: 3em;
    line-height: 143%;
    p{
      line-height: inherit;
    }
  }
}

.team-doctors{
  padding: 6.25em 0 4.125em;
  overflow: hidden;
  @media screen and (min-width: 577px){
    .how-recover__slider{
      position: relative;
    }
    .swiper{
      &-slide{
        &:nth-child(1),
        &:nth-child(2){
          padding-bottom: 12.25em;
        }
      }
    }
    .team-consult{
      position: absolute;
      width: 100%;
      top: 21.375em;
      left: 0;
      z-index: 2;
    }
  }
}

.team-consult{
  padding: 1.875em 2.5em 2.5em 4.375em;
  background: radial-gradient(62.23% 670.3% at 91.8% 13.33%, #9E9396 0%, #80787A 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
  box-shadow: 0px 1.5em 3.125em rgba(119, 110, 112, 0.5);
  border-radius: .75em;
  display: flex;
  justify-content: space-between;
  color: $lightGray;
  &__title{
    position: relative;
    padding-top: .43em;
    img{
      position: absolute;
      right: 100%;
      top: 0;
      width: .7em;
      margin-right: .35em;
    }
  }
  &__desc{
    margin-top: .75em;
  }
  &__btn{
    img{
      width: 1.2em;
      margin-right: .5em;
    }
  }
}

.cases{
  padding: 3em 0 3.125em;
  &__slider {
    .swiper {
      @media screen and (min-width: 577px) {
        &-wrapper {
          display: grid;
          grid-template-columns: repeat(3, calc(100% / 3 - 2em));
          gap: 3em;
          transform: unset !important;
        }
      }
    }
  }
}

.team-edu{
  padding: 3.125em 0 4.875em;
  &__list{
    margin-top: 1.5em;
    max-width: 49em;
  }
}


@media screen and (max-width: 576px){
  .team{
    padding: 0 0 1.875em;
    background-image: none !important;
    .container{
      padding: 0;
    }
    &__wrap{
      padding: 1.25em 1.5em 1.875em;
      background-size: cover;
      background-position: right bottom;
    }
    &__text{
      margin: 0;
      gap: .625em;
      max-width: 14em;
    }
    &__desc{
      display: none;
    }
    &__info{
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25em;
      &-content{
        order: 2;
        margin: 0;
      }
    }
    .selection-card__images{
      margin: .5em 0 0;
    }
    &__bottom{
      display: block;
      margin: 0;
    }
    &__right{
      padding: 3em 1.5em .75em;
    }
  }

  .for-who{
    padding: 1.25em 1.5em 2.5em;
    background-color: $blockBg;
    &__list{
      font-size: 1em;
      margin-top: .75em;
      gap: .375em;
    }
    &__nav{
      margin-top: 1.875em;
    }
    &__btn{
      height: 3.5em;
      padding: 0 .5em;
      width: 100%;
    }
    &__text{
      display: block;
      margin-bottom: 1.25em;
    }
  }

  .why-team{
    width: 100%;
    padding: 1.5em;
    &__img{
      width: 6em;
    }
  }

  .team-doctors{
    padding: 3em 0 1.875em;
  }

  .team-consult{
    margin-top: 3.75em;
    padding: 1em 1.5em 1.5em 2.75em;
    display: block;
    &__info{
      margin-bottom: .625em;
    }
    &__title{
      img{
        width: .75em;
      }
    }
    &__desc{
      margin-top: .375em;
    }
    &__btn{
      padding: 0 1em;
      img{
        width: 1.3em;
      }
    }
  }

  .cases{
    padding: 1.875em 0;
    .swiper{
      &-slide{
        width: 18.23em;
        padding-right: .75em;
      }
    }
  }

  .team-edu{
    padding: 1.875em 0;
  }
}
