.cases-page{
  padding-bottom: 6.25em;
  .filter{
    padding: 0;
    margin-top: 1.25em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;
    margin-top: 3em;
  }
  &__more{
    margin-top: 3em;
  }
}

.cases-page2{
  padding: 4.75em 0 6.25em;
  .cases-page__grid{
    margin: 0;
  }
}

.cases-collection{
  overflow: hidden;
  padding: 6.25em 0;
  background-color: $blockBg;
}

.case-page{
  padding-bottom: 5.375em;
  background-color: $blockBg;
  &__inner{
    padding-top: .375em;
    display: grid;
    grid-template-columns: 39.75% calc(60.25% - 3.75em);
    gap: 3.75em;
  }
  &__text{
    margin-top: 1.25em;
  }
  &__by{
    margin-top: 2.5em;
  }
  &__doctors{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
    .author{
      margin: 0;
    }
  }
  &__images{
    position: relative;
    padding-bottom: 77.755%;
    &-grid{
      position: absolute;
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1px));
      gap: 2px;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      &:first-child{
        border-radius: .75em 0 0 .75em;
      }
      &:last-child{
        border-radius: 0 .75em .75em 0;
      }
    }

  }
  .swiper{
    &-container{
      overflow: hidden;
      position: relative;
    }
  }
  .case-card__badge{
    left: 1em;
  }
}

.patient-req{
  padding: 6.25em 0 3.125em;
  &__text{
    margin-top: 1.25em;
  }
}

.what-done{
  padding: 3.125em 0;
  overflow: hidden;
  &__groups{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: 4.75em;
  }
  &__group{
    &-grid{
      display: grid;
      grid-template-columns: 60.623% 30.859%;
      justify-content: space-between;
    }
    .author{
      margin: 1.25em 0 2.5em;
    }
  }
  &__text{
    max-width: 90%;
  }
  &__slider{
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(3,calc((100% / 3) - (2em / 3)));
          gap: 1em;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.patient-finally{
  padding: 3.125em 0;
  overflow: hidden;
}


@media screen and (max-width: 576px){
  .cases-page{
    padding-bottom: 3em;
    .filter{
      margin-top: 3em;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2.25em;
      gap: 1.5em;
    }
    &__more{
      margin-top: 1.5em;
    }
  }

  .cases-page2{
    padding: 3em 0 1.875em;
  }

  .cases-collection{
    padding: 3em 0;
    .swiper{
      &-slide{
        width: 19.5em;
        padding-right: .75em;
      }
    }
  }

  .case-page{
    padding-bottom: 3em;
    &__inner{
      padding: 0;
      grid-template-columns: 100%;
      gap: 1.75em;
    }
    &__right{
      order: -1;
      width: calc(100% + 3em);
      margin-left: -1.5em;
    }
    &__images{
      img{
        border-radius: 0 !important;
      }
    }
    .swiper{
      &-pagination{
        margin-top: .875em;
      }
    }
    &__text{
      display: none;
    }
    &__by{
      margin-top: 2.125em;
    }
    &__doctors{
      margin-top: .625em;
    }
  }

  .patient-req{
    padding: 3em 0 1.875em;
    &__text{
      margin-top: .625em;
    }
  }

  .what-done{
    padding: 1.875em 0;
    &__groups{
      margin-top: 1.5em;
      gap: 3em;
    }
    &__group {
      &-grid {
        grid-template-columns: 100%;
        gap: 1.5em;
      }
      .author{
        margin: .5em 0 1.5em;
      }
    }
    &__slider{
      margin-top: 1.5em;
      .swiper{
        &-slide{
          width: 19em;
          padding-right: .75em;
        }
      }
    }
  }

  .patient-finally{
    padding: 1.875em 0;
  }
}
