.sales{
  padding-bottom: 3em;
  &__desc{
    margin-top: 1.25em;
    max-width: 48.5em;
  }
  &__grid{
    margin-top: 5.375em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;
  }
}

.sale-card{
  background-size: cover;
  background-position: right bottom;
  background-repeat: no-repeat;
  padding: 2.5em 8em 2.5em 2.5em;
  border-radius: .75em;
  display: flex;
  flex-direction: column;
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: .375em;
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2em;
  }
}

.sale-consult,
.sales-collection{
  &__slider{
    margin-top: 0;
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(3,calc(100% / 3 - 2em));
          gap: 3em;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.sale-consult{
  background-color: $blockBg;
  padding: 2.5em 0;
}

.sale-consult-card{
  padding: 2.5em 2.5em 1.5em;
  background-color: $lightGray;
  border-radius: .75em;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  &__text{
    margin: 1.25em 0;
  }
  &__title,
  &__free{
    line-height: 127%;
  }
  &__nav{
    display: flex;
    align-items: center;
    padding-top: 2em;
    margin-top: auto;
  }
  &__btn{
    margin-right: .72em;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 6.75em;
    pointer-events: none;
  }
}

.sales-collection{
  padding: 3em 0 3.125em;
}

.sale{
  .team{
    &__text{
      max-width: 48.5em;
    }
    &__bottom{
      margin-top: 3.75em;
    }
  }
  &__prices{
    display: flex;
    align-items: center;
    margin-top: 4.375em;
  }
  &__discount{
    align-self: flex-start;
    margin-left: .625em;
    display: flex;
    align-items: center;

    &-percent{
      padding: 0 .5em;
      color: #FFFFFF;
      background: $primary;
      backdrop-filter: blur(.4375em);
      border-radius: .375em;
      display: flex;
      align-items: center;
      height: 1.625em;
      margin-left: .63em;
    }
  }
  &__price2{
    font-size: 1.5em;
    line-height: 120%;
  }
  .for-who{
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      padding: 0 1.85em;
      height: 3.4em;
    }
  }
}

.sale-condition{
  padding: 6.25em 0 3.125em;
  &__text{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
    max-width: 48.5em;
    p{
      line-height: inherit;
    }
  }
}

.sale-doctors{
  padding: 3.125em 0;
  &__slider{
    margin-top: 2.5em;
  }
  @media screen and (min-width: 576px){
    .submenu__doctors-grid{
      grid-template-columns: repeat(4,1fr);
    }
  }
}

.other-sales{
  padding: 3.125em 0;
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;
  }
  &__more{
    margin-top: 3em;
  }
}



@media screen and (max-width: 576px){
  .sales{
    padding: 1em 0 3.75em;
    &__desc{
      display: none;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 2.25em;
    }
  }

  .sale-card,
  .sale-consult-card{
    padding: 1.5em 5.75em 1.5em 1.5em;
    &__nav{
      padding-top: .625em;
    }
  }

  .sale-consult,
  .sales-collection{
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 19.5em;
        padding-right: .75em;
      }
    }
  }

  .sale-consult{
    padding: 3.75em 0;
  }

  .sales-collection{
    padding: 3.75em 0 3.125em;
  }

  .sale{
    padding-bottom: 0;
    .team{
      &__text{
        max-width: 14em;
      }
      &__desc{
        display: block;
      }
      &__title{
        max-width: 10em;
      }
      &__wrap{
        padding-bottom: 4.625em;
      }
      &__bottom{
        margin: 0;
      }
    }
    .for-who{
      padding-top: 1.5em;
      padding-bottom: 3.75em;
    }
    &__prices{
      margin-top: 2.125em;
    }
    &__discount{
      align-self: unset;
    }
    &__price2{
      font-size: 1.25em;
    }
  }

  .sale-condition{
    padding: 3em 0 1.875em;
    &__text{
      margin-top: .6em;
      gap: 2em;
    }
  }

  .sale-doctors{
    padding: 1.875em 0;
    overflow: hidden;
    &__slider{
      margin-top: 1.5em;
    }
  }

  .other-sales{
    padding: 1.875em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 1.5em;
    }
    &__more{
      margin-top: 1.5em;
    }
  }
}
