.display1{
  font-size: 3.875em;
  line-height: 119%;
  font-weight: 400;
}

.content-text h2,
.display2{
  font-size: 2.375em;
  line-height: 127%;
  font-weight: 400;
}

.content-text h3,
.display3{
  font-size: 1.75em;
  line-height: 122%;
  font-weight: 400;
}

.display4{
  font-size: 1.375em;
  line-height: 150%;
  font-weight: 400;
}

.display5{
  font-size: 1em;
  line-height: 127%;
  font-weight: 400;
}

.text18{
  font-size: 1.125em;
  line-height: 171%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text14{
  font-size: 0.875em;
  line-height: 171%;
}

.text12{
  font-size: 0.75em;
  line-height: 133%;
}

.gray-text{
  color: rgba(128, 120, 122, 0.6);
}

.gray-text2{
  color: rgba(231, 225, 217, 0.6);
}

.accent-text{
  color: $primary;
}

.custom-display{
  padding-top: 0.14517em;
  span{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: .42em;
      height: .42em;
      top: 0;
      left: 100%;
      background-image: url(../img/brand-icon.svg);
      background-size: 100% auto;
      background-repeat: no-repeat;
      background-position: center;
    }
  }
  img{
    width: .42em;
    transform: translate(-.25em,-.6em);
  }
  &.display3{
    padding-top: .36em;
    span{
      &:before{
        width: .645em;
        height: .645em;
      }
    }
  }
}

.m-ul{
  display: grid;
  grid-template-columns: 100%;
  gap: 1em;
  li{
    line-height: inherit;
    display: flex;
    &:before{
      content: '';
      background-color: $primary;
      background-image: url(../img/check3.svg);
      width: 1em;
      height: 1em;
      display: block;
      margin-right: .75em;
      flex-shrink: 0;
      background-size: 50% auto;
      border-radius: 50%;
      background-repeat: no-repeat;
      background-position: center;
      transform: translateY(.25em);
    }
  }
}

.m-ul2{
  display: grid;
  grid-template-columns: 100%;
  gap: 1.5em;
  li{
    line-height: inherit;
    display: flex;
    &:before{
      content: '';
      background-color: $primary;
      width: .25em;
      height: .25em;
      border-radius: .0625em;
      display: block;
      margin-right: 1.125em;
      flex-shrink: 0;
      transform: translateY(.625em);
    }
  }
}

.content-text{
  h2{
    &:not(:last-child){
      margin-bottom: .54em;
    }
  }
  h3{
    &:not(:last-child){
      margin-bottom: .715em;
    }
  }
  hr{
    border: none;
    margin: 1.5em 0;
  }
  p,
  ul,
  ol{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: .5em;
    }
  }
  li{
    line-height: inherit;
  }
  ul{
    padding-left: .75em;
    display: grid;
    grid-template-columns: 100%;
    gap: .125em;
    li{
      display: flex;
      &:before{
        content: '';
        width: 3px;
        height: 3px;
        border-radius: 50%;
        display: block;
        margin-right: .75em;
        flex-shrink: 0;
        background-color: $black;
        transform: translateY(.6em);
      }
    }
  }
  ol{
    counter-reset: listNum;
    padding-left: .8em;
    &>li{
      padding-left: 1.0625em;
      position: relative;
      &:before{
        counter-increment: listNum;
        content: counter(listNum) '.';
        color: $primary;
        position: absolute;
        left: 0;
        top: 0;
      }
      &:not(:last-child){
        margin-bottom: 1em;
      }
    }
  }
  blockquote{
    font-size: 1.125em;
    padding: 3.02em 2.28em;
    line-height: 156%;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      background-size: 100% auto;
      background-repeat: no-repeat;
      width: 1.2em;
      height: 1.2em;
    }
    &:before{
      bottom: 0;
      left: 0;
      background-image: url(../img/quote2.svg);
      background-position: left bottom;
    }
    &:after{
      top: 0;
      right: 0;
      background-image: url(../img/quote.svg);
      background-position: right top;
    }
  }
}


@media screen and (max-width: 576px){
  .display1{
    font-size: 2em;
  }

  .content-text h2,
  .display2{
    font-size: 1.5em;
  }

  .display4{
    font-size: 1.125em;
    line-height: 122%;
  }

  .display3{
    font-size: 1.25em;
  }

  .display5{
    font-size: 0.875em;
  }

  .text18{
    font-size: 1em;
  }

  .text16{
    font-size: 0.875em;
  }

  .text14{
    font-size: 0.75em;
    line-height: 150%;
  }

  .text12{
    font-size: 0.63em;
  }

  .mob-display2{
    font-size: 1.5em;
  }

  .mob-display3{
    font-size: 1.25em;
  }

  .mob-display4{
    font-size: 1.125em;
  }

  .mob-text18{
    font-size: 1.125em;
  }

  .mob-text16{
    font-size: 1em;
  }

  .mob-text12{
    font-size: .75em;
  }

  .mob-text14{
    font-size: .875em;
  }

  .mob-text10{
    font-size: .625em;
  }

  .content-text{
    h3{
      font-size: 1.43em;
    }
    hr{
      margin: .75em 0;
    }
    blockquote{
      font-size: 1.142em;
      padding: .44em .75em;
      &:before,
      &:after{
        width: .625em;
      }
    }
  }
}
