@import "../vars.scss";
.breadcrumbs {
    margin: 1.69em 0 2.5em;
    &__item {
        font-size: .625em;
        line-height: 140%;
        position: relative;
        font-weight: 400;
        display: inline;
        &:not(:last-child){
            margin-right: 1.2em;
        }
        &:not(:first-child) {
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: .33em;
                height: .33em;
                background-color: $primary;
                border-radius: .1em;
                top: .4em;
                right: 100%;
                margin-right: .7em;
            }
        }
        &:last-child{
            color: rgba(128, 120, 122, 0.6);
        }
    }
}

@media screen and (max-width: 576px){
    .breadcrumbs{
        display: none;
    }
}
