.consults{
  padding-bottom: 3.75em;
  &__grid{
    margin-top: 4.75em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;

    .info__inner{
      grid-column: 1/-1;
    }
  }
}

.consult-page{
  background-color: $blockBg;
  &__inner{
    display: grid;
    grid-template-columns: auto 46.33%;
    gap: 1em;
  }
  &__left{
    padding-bottom: 6.25em;
  }
  &__right{
    display: flex;
    align-items: flex-end;
    background-image: url(../img/k.svg);
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: 100% auto;
  }
  &__img{
    width: 100%;
  }
  &__info{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__list{
    line-height: 156%;
    gap: .89em;
    li{
      &:before{
        margin-right: .445em;
      }
    }
  }
  &__available{
    margin-top: 2.5em;
  }
  &__price{
    margin-top: 3.125em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
  &__btn{
    &:not(:last-child){
      margin-right: 1em;
    }
    img{
      width: 1.2em;
      margin-right: .5em;
      flex-shrink: 0;
    }
    &--order{
      background-color: #fff;
    }
  }
}

.consult-desc{
  padding: 6.25em 0 3.125em;
  .patient-req__text{
    max-width: 48.5em;
  }
}

.consult-contain{
  padding: 3.125em 0;
}

.docs,
.consult-collection{
  background-color: transparent;
  padding: 3.125em 0;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;
  }
}

.docs{
  &__grid{
    margin-top: 3em;
  }
}

.doc-card{
  padding: 2.5em;
  border: 1px solid $lightGray;
  border-radius: .75em;
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: .75em;
    margin-top: 1em;
  }
  &__item{
    display: flex;
  }
  &__num{
    min-width: 1em;
    flex-shrink: 0;
  }
  &__nav{
    padding: 1em 0 0 1em;
    display: flex;
  }
}


@media screen and (max-width: 576px) {
  .consults {
    padding: 1em 0 1.875em;
    &__grid {
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 3.75em;
      .info__inner{
        grid-row: 5/6;
      }
    }
  }

  .consult-page{
    padding: 1.5em 0 3.75em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__left{
      padding: 0;
    }
    &__right{
      display: none;
    }
    &__info{
      margin-top: 1.5em;
      gap: .75em;
    }
    &__list{
      gap: .375em;
    }
    &__available{
      margin-top: 1.5em;
    }
    &__price{
      margin-top: 2.125em;
    }
    &__nav{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: .75em;
    }
    &__btn {
      padding: 0;
      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }

  .consult-desc{
    padding: 3.75em 0 1.875em;
  }

  .consult-contain{
    padding: 1.875em 0;
  }

  .consult-collection,
  .docs{
    padding: 1.875em 0;
    &__grid{
      margin-top: .875em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }


  .docs{
    &__grid{
      margin-top: .625em;
    }
  }

  .doc-card{
    padding: 1.5em;
    &__items{
      margin-top: .625em;
      gap: .375em;
    }
    &__num{
      min-width: 1.145em;
    }
  }
}
