.doctors{
  &__wrap{
    display: grid;
    grid-template-columns: 48.4% auto;
    background: radial-gradient(41.68% 149.99% at 71.6% 50.2%, #F8F7F7 0%, #E7E1D9 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    box-shadow: 0px 4px 20px rgba(119, 110, 112, 0.1);
    border-radius: 12px;
    margin-top: 2.5em;
  }
  &__info{
    padding: 2.5em;
  }
  &__desc{
    margin-top: 1.25em;
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
  &__slider{
    padding: 2.5em 4.125em;
    overflow: hidden;
    .swiper{
      &-container{
        margin: 0 auto;
        width: 10em;
        transform: translateX(-1.7em);
      }
      &-slide{
        display: block;
        height: 10em;
        padding: .5em;
        transition: all .3s;
        &:before{
          content: '';
          position: absolute;
          background: linear-gradient(90deg, #E7E1D9 -0.14%, rgba(231, 225, 217, 0) 99.55%);
          top: 50%;
          left: 50%;
          width: calc(100% - 0em);
          height: calc(100% - 0em);
          transform: translate(-50%,-50%);
          transition: all .3s;
          border-radius: 50%;
        }
        img{
          width: 100%;
          transition: all .3s;
        }
        &-active{
          background-image: url(../img/img-border.svg);
          background-size: 100% auto;
          background-position: center;
          background-repeat: no-repeat;
        }
        &:not(.swiper-slide-active){
          opacity: 0.5;
          &:not(.swiper-slide-prev){
            &:not(.swiper-slide-next){
              transform: translateX(5em) scale(.65);
            }
          }
        }
        &-prev{
          transform: translateX(1.5em) scale(.65);
          &:before{
            opacity: 0;
          }
          &~.swiper-slide{
            &:before{
              opacity: 0;
            }
          }
        }
        &-next{
          transform: translateX(-1.5em) scale(.65);
          &+.swiper-slide{
            transform: translateX(-5em) scale(.65) !important;
            &+.swiper-slide{
              transform: translateX(-8.5em) scale(.65) !important;
            }
          }
        }
      }
    }
  }
  &__more{
    padding: 1.5em 0;
    &-btn{
      width: 100%;
      svg{
        width: 1.125em;
        height: 1.125em;
      }
    }
  }
}

.filter{
  padding: 3.5em 0 1.5em;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: calc(100% +  1.25em);
  &__switch{
    display: flex;
    align-items: center;
    margin-top:  1.25em;
    .m-switch{
      margin-right: .625em;
    }
  }
}

.filter-select{
  margin-top:  1.25em;
  position: relative;
  &__title{
    margin-bottom: 1.2em;
    @media screen and (min-width: 577px){
      display: none;
    }
  }
  &._active{
    .filter-select__show{
      pointer-events: none;
    }
    .filter-select__dropdown{
      opacity: 1;
      pointer-events: unset;
      @media screen and (min-width: 577px){
        transform: translateY(0);
      }
    }
  }
  &:not(:last-child){
    margin-right: 1.25em;
  }
  &__show,
  .m-select .current{
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    height: 2.625em;
    border: 1px solid $lightGray;
    border-radius: .375em;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      border-color: $primary;
    }
    svg{
      width: .625em;
      height: auto;
      margin-left: .875em;
    }
  }
  .m-select{
    float: unset;
    color: inherit;
    padding: 0;
    width: unset;
    height: 2.625em;
    background-color: transparent !important;
    border: none !important;
    font-size: 1em;
    .current{
      padding-right: 3.15em;
      font-size: .875em;
      height: 100%;
      background-image: url(../img/select-arrow.svg);
      background-repeat: no-repeat;
      background-position: right 1.45em center;
    }
    &.open{
      .list{
        @media screen and (min-width: 577px){
          transform: translateY(0);
        }
      }
    }
    .list{
      margin-top: .625em;
      transition: all .3s;
      width: unset;
      min-width: 100%;
      background: $black;
      color: $lightGray;
      box-shadow: 0px 4px 80px rgba(119, 110, 112, 0.3);
      border-radius: .75em;
      padding: 2.5em;
      font-size: 1em;
      display: grid;
      grid-template-columns: 100%;
      gap: 1em;
      @media screen and (min-width: 577px){
        transform: translateY(2em);
      }
    }
    .option{
      white-space: nowrap;
      font-size: 1.125em;
      padding: 0;
      border: none !important;
      &:hover,
      &.focus,
      &.selected.focus {
        background-color: transparent;
        font-weight: 500;
      }
      &.disabled{
        display: none;
        font-weight: 400 !important;
        pointer-events: none;
      }
    }
  }
  &__dropdown{
    z-index: 3;
    position: absolute;
    min-width: 22em;
    max-width: calc(100vw - 1.75em);
    background: $black;
    color: $lightGray;
    box-shadow: 0px 4px 80px rgba(119, 110, 112, 0.3);
    border-radius: .75em;
    padding: 2.5em;
    top: 100%;
    left: 0;
    margin-top: .625em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    @media screen and (min-width: 577px){
      transform: translateY(2em);
    }
  }
  &__checks{
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
  .m-radio{
    label{
      font-size: 1em;
      &:before{
        width: 1.3em;
        height: 1.3em;
      }
    }
    .filter-select__doctor-num{
      margin-left: .5em;
    }
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
  }
  &__apply{
    font-size: 1em;
    padding: 0 1.25em;
    height: 3.125em;
    img{
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__refresh{
    color: $lightGray;
    white-space: nowrap;
    margin-right: 2.145em;
    img{
      width: 1.15em;
      margin-right: .45em;
    }
  }
  &__doctor{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    .author{
      opacity: 0.4;
      &__img{
        position: relative;
        &:before{
          content: '';
          position: absolute;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          border: 2px solid $primary;
          top: 0;
          left: 0;
          z-index: 2;
          opacity: 0;
        }
      }
    }
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      z-index: 2;
      cursor: pointer;
      &:checked{
        &+.author{
          opacity: 1;
          .author__img{
            border-color: $primary;
            &:before{
              opacity: 1;
            }
          }
        }
        &~.filter-select__doctor-num{
          background-color: $primary;
        }
      }
    }
    &-num{
      margin-left: 1em;
      flex-shrink: 0;
      padding: 0 .571em;
      height: 2em;
      min-width: 1.8em;
      border-radius: .4em;
      background-color: $gray;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.doctors-collection{
  padding: 1.5em 0;
}

.doctor-consult{
  padding: 1.875em 5em 2.5em 2.5em;
  background: radial-gradient(62.23% 670.3% at 91.8% 13.33%, #9E9396 0%, #80787A 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
  border-radius: .75em;
  height: 100%;
  color: $lightGray;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    width: 5.625em;
  }
  &__desc{
    margin-top: .625em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
}

.doctor-consult2{
  padding-right: 2.5em;
  padding-left: 2.75em;
  .doctor-consult__title{
    position: relative;
    img{
      position: absolute;
      right: 100%;
      bottom: 100%;
      width: .72em;
      transform: translateY(.24em);
    }
  }
  @media screen and (min-width: 577px){
    .doctor-consult__btn{
      font-size: 1em;
      padding: 0 1.25em;
      height: 3.125em;
      img{
        width: 1.125em;
        margin-right: .5em;
      }
    }
  }
}

.about-doctors{
  padding: 4.75em 0;
  &__inner{
    display: grid;
    grid-template-columns: 31% auto;
    gap: .625em;
    background-image: url(../img/about-bg2.png);
    background-size: cover;
    background-position: right center;
  }
  &__num{
    color: $primary;
    font-size: 4.375em;
  }
  &__desc{
    max-width: 22.25em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .doctors{
    padding: 1em 0 .75em;
    &__wrap{
      margin-top: 2em;
      grid-template-columns: 100%;
      padding-bottom: 4em;
      position: relative;
    }
    &__info{
      padding: 1.5em 1.5em 0;
    }
    &__subtitle{
      text-align: center;
    }
    &__desc{
      display: none;
    }
    &__nav{
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 1.5em;
      justify-content: center;
    }
    &__slider{
      padding: .75em 1.5em;
      .swiper{
        &-container{
          transform: unset;
          width: 6.14em;
        }
        &-slide{
          height: 6.14em;
          padding: .3125em;
          &:not(.swiper-slide-active){
            &:not(.swiper-slide-prev){
              &:not(.swiper-slide-next){
                transform: translateX(3em) scale(.65);
              }
            }
          }
          &-prev{
            transform: translateX(1em) scale(.65);
          }
          &-next{
            transform: translateX(-1em) scale(.65);
            &+.swiper-slide{
              transform: translateX(-3em) scale(.65) !important;
              &+.swiper-slide{
                transform: translateX(-5.5em) scale(.65) !important;
              }
            }
          }
        }
      }
    }
    &__more{
      padding: 0 0 .75em;
    }
  }

  .filter{
    padding: 2.625em 0 .75em;
    width: calc(100% + .625em);
    &__switch{
      width: 100%;
    }
  }

  .filter-select{
    margin-top: .625em;
    &:not(:last-child){
      margin-right: .625em;
    }
    &__show{
      padding: 0 .5em;
      height: 1.8em;
      svg{
        margin-left: .625em;
      }
    }
    .m-select{
      height: 1.8em;
      .current{
        font-size: .75em;
        padding: 0 2.33em 0 .5em;
        background-position: right .9em center;
      }
      .option{
        font-size: 1em;
        &:not(:last-child){
          margin-bottom: 1em;
        }
        &.disabled{
          display: block;
          font-size: 1.5em;
        }
      }
      .list{
        display: block;
      }
    }
    &__dropdown,
    .m-select .list{
      padding: 1.5em;
      max-width: unset;
      max-height: 100vh;
      width: 100vw;
      overflow-y: auto;
      border-radius: 0;
      position: fixed;
      top: unset;
      bottom: 0;
      left: 0;
      margin: 0;
    }
    &__refresh{
      font-size: .75em;
    }
    &__apply{
      font-size: .875em;
      padding: 0 1em;
      height: 2.9em;
    }
  }

  .doctors-collection{
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: 100%;
        gap: 1.5em;
      }
      &-slide{
        width: 100%;
        padding: 0;
      }
    }
  }

  .doctor-consult{
    padding: 1.5em 5em 1.5em 1.5em;
    &__desc,
    &__nav{
      margin-top: .375em;
    }
  }

  .doctor-consult2{
    padding-left: 2.75em;
    .doctor-consult__btn{
      font-size: .875em;
      padding: 0 1em;
      height: 2.9em;
      border-radius: .575em;
      img{
        width: 1.285em;
        margin-right: .5em;
      }
    }
  }

  .about-doctors{
    padding: 3em 0;
    background-image: url(../img/about-bg3.png);
    background-position: right center;
    background-repeat: no-repeat;
    background-size: cover;
    &__inner{
      padding-right: 3em;
      grid-template-columns: 100%;
      gap: 1.125em;
      background: unset;

    }
    &__info{
      max-width: 15em;
    }
  }
}
