.gallery__small .swiper-slide{
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.gallery__small .swiper-slide img{
  object-fit: unset;
  width: 100%;
  height: auto;
}

.modification__img{
  overflow: hidden;
}

.product-card__img img{
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
}

.gallery__img img {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.gallery__img{
  overflow: hidden;
}

.gallery__img img {
  height: auto;
}

.gallery__small .swiper-slide{
  display: flex;
  align-items: center;
  justify-content: center;
}
